import Vue from 'vue';
import * as Sentry from '@sentry/browser';
import * as Integrations from '@sentry/integrations';
import {
    isString,
} from '@/lib/utils';

if (process.env.NODE_ENV === 'production' && String(process.env.RO_ENABLE_SENTRY) === 'true') {
    Sentry.init({
        // 项目的地址，用于收集错误信息的 sentry 分配的地址
        dsn: 'https://f2650d220ae14b69998b24f54940038b@sentry.io/1422705',
        // Vue集成Sentry
        integrations: [new Integrations.Vue({ Vue, attachProps: true })],
        // 过滤一些错误
        beforeSend(event, hint) {
            const filterMessageList = [ 'WeixinJSBridge is not defined', 'Network Error' ];
            let eventMessage: string | undefined;
            if (event) {
                eventMessage = event.message;
            }
            if (hint) {
                if (hint.originalException) {
                    const exception = hint.originalException;
                    if (isString(exception)) {
                        eventMessage = exception;
                    } else {
                        eventMessage = exception.message;
                    }
                }
            }
            if (eventMessage) {
                for (let i = 0, len = filterMessageList.length; i < len; i++) {
                    if (eventMessage.indexOf(filterMessageList[i]) >= 0) {
                        return null;
                    }
                }
            }
            return event;
        },
    });
}
