import Vue from 'vue';
import store from '.';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import badge from '@/router/badge';
export interface BadgeState {
    /** 勋章卡的基本信息 */
    badgesBasicInfo: {
        insist: Badge.BasicInfo[],
        distance: Badge.BasicInfo[],
        holiday: Badge.BasicInfo[],
    };
    /** 点亮勋章卡的数量 */
    lightBagesCount: number;
    /** 勋章卡信息 */
    badgeDetails: {
        [cardId: string]: Badge.DetailsInfo,
    };
    /** 我的数据 */
    myData: {
        requesting: boolean;
        loaded: boolean;
        list: Badge.MyData[];
    };
}
const m: Module<BadgeState, RootState> = {
    namespaced: true,
    state: {
        badgesBasicInfo: {
            insist: [],
            distance: [],
            holiday: [],
        },
        badgeDetails: {},
        lightBagesCount: 0,
        myData: {
            requesting: false,
            loaded: false,
            list: [],
        },
    },
    getters: {},
    mutations: {
        /** 设置所有勋章卡的基本信息 */
        setBadgesBasicInfo(state, { count, info, activityId }: { count: number, info: Badge.BasicInfo[], activityId: string }) {
            state.badgesBasicInfo.insist = [];
            state.badgesBasicInfo.distance = [];
            state.badgesBasicInfo.holiday = [];
            info.forEach((item) => {
                switch (item.type) {
                    case 'insist':
                        state.badgesBasicInfo.insist.push(item);
                        break;
                    case 'distance':
                        state.badgesBasicInfo.distance.push(item);
                        break;
                    case 'holiday':
                        state.badgesBasicInfo.holiday.push(item);
                        break;
                }
            });
            state.lightBagesCount = count;
        },
        /** 设置勋章卡详情 */
        setBadgeDetails(state, { cardId, info }: { cardId: string, info: Badge.DetailsInfo }) {
            Vue.set(state.badgeDetails, String(cardId), info);
        },
        setMyDataRequest(state, request) {
            state.myData.requesting = request;
        },
        setMyDataLoaded(state, loaded) {
            state.myData.loaded = loaded;
        },
        concatMyData(state, list) {
            state.myData.list = state.myData.list.concat(list);
        },
    },
    actions: {
        /**
         * 获取所有勋章卡的信息
         */
        async getBadgesBasicInfo({ state, commit }, { activityId, userId }) {
            const response = await Vue.axios.get('/api/v1/medal/medal_list', {
                params: {
                    activity_id: activityId,
                    user_id: userId,
                },
            });
            const rdata = response.data.data;
            if (response.data.status === 0) {
                commit('setBadgesBasicInfo', {
                    count: rdata.count,
                    info: rdata.data,
                    activityId,
                });
            }
            return rdata;
        },
        /**
         * 获取勋章卡详情
         */
        async getBadgeDetails({ state, commit }, { activityId, cardId }: { activityId: string, cardId: string }) {
            const response = await Vue.axios.get('/api/v1/medal/medal_detail', {
                params: {
                    activity_id: String(activityId),
                    medal_id: String(cardId),
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setBadgeDetails', {
                    cardId,
                    info: rdata.data,
                });
            }
            return rdata;
        },
        /**
         * 获取我的数据
         */
        async getMyData({ state, commit }, { activityId, count, offset, forceUpdate }: { activityId: string, count: number, offset: number, forceUpdate: boolean }) {
            if (state.myData.requesting || (state.myData.loaded && !forceUpdate)) {
                return;
            }
            if (offset === 0 && forceUpdate) {
                state.myData.list = [];
                state.myData.loaded = false;
                state.myData.requesting = false;
            }
            const end = offset + count;
            if (end <= state.myData.list.length) {
                return;
            }
            if (offset < state.myData.list.length) {
                offset = state.myData.list.length;
                count = end - offset;
            }
            commit('setMyDataRequest', true);
            const response = await Vue.axios.get('/api/v1/user/week_card', {
                params: {
                    activity_id: activityId,
                    offset,
                    count,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('concatMyData', rdata.data);
                if (rdata.data.length < count) {
                    commit('setMyDataLoaded', true);
                }
            }
            commit('setMyDataRequest', false);
            return rdata;
        },
    },
};
export default m;
