import Schema, { Model } from '@forzoom/models';
import { RuntimeType } from '.';
@RuntimeType({
    models: {
        name: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormInput',
                title: '跑团名称',
                placeholder: '请输入跑团名称（不超过8个字）',
            },
        },
        districtPlaceholder: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormPlaceholder',
                title: '跑团地区',
                placeholder: '请选择',
            },
        },
        // 成立时间
        startDate: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormInput',
                subtype: 'text',
                title: '跑团成立年份',
                placeholder: '请填写，如2018',
            },
        },
        // 活动场所
        activityFactory: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormInput',
                subtype: 'text',
                title: '跑团活动场所',
                placeholder: '请输入活动场所',
            },
        },
        leader: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormInput',
                title: '团长姓名',
                placeholder: '请输入姓名',
            },
        },
        mobile: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormInput',
                subtype: 'tel',
                title: '联系方式',
                placeholder: '请输入手机号',
            },
        },
        // 物料收货地址
        mailingPlaceholder: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormPlaceholder',
                title: '物料收货地区',
                placeholder: '请选择',
            },
        },
        districtLevel1: {
            type: 'String?',
            extra: {
                display: false,
            },
        },
        districtLevel2: {
            type: 'String?',
            extra: {
                display: false,
            },
        },
        districtLevel3: {
            type: 'String?',
            extra: {
                display: false,
            },
        },
        mailingAddress1: {
            type: 'String?',
            extra: {
                display: false,
            },
        },
        mailingAddress2: {
            type: 'String?',
            extra: {
                display: false,
            },
        },
        mailingAddress3: {
            type: 'String?',
            extra: {
                display: false,
            },
        },
        mailingAddress: {
            type: 'String',
            default: '',
            extra: {
                type: 'FormInput',
                title: '物料收货详细地址',
                placeholder: '请输入详细地址，xx单元，xx室',
            },
        },
        description: {
            type: 'String',
            default: '',
            extra: {
                type: 'ItemTextarea',
                title: '跑团介绍',
                placeholder: '请输入现有规模，成员人数，跑团成绩，如：跑团成员100人，30位参加过马拉松。队长是中国红十字会急救员，百公里挑战者，健康跑团团长，骑行爱好者，越野赛事挑战者。',
            },
        },
        image: {
            type: 'Array',
            default() {
                return [];
            },
        },
        imageKey: {
            type: 'Array',
            default() {
                return [];
            },
        },
        isLock: {
            type: 'Number',
            default: 0,
        },
    },
})
export default class Identification extends Schema {
    public static models?: Model;
    public name!: string;
    public districtPlaceholder!: string;
    public startDate!: string;
    public activityFactory!: string;
    public leader!: string;
    public mobile!: string;
    public mailingPlaceholder!: string;
    public districtLevel1!: string | null;
    public districtLevel2!: string | null;
    public districtLevel3!: string | null;
    public mailingAddress1!: string | null;
    public mailingAddress2!: string | null;
    public mailingAddress3!: string | null;
    public mailingAddress!: string;
    public description!: string;
    /**  图片信息 */
    public image!: Array<{
        url: string,
        key: string,
    }>;
    public imageKey!: string[];
    public isLock!: number;

    constructor(data?: object) {
        super(Identification.models!, data);
        this._name = Identification.name;
    }
}
