




import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { isUndef } from '@/lib/utils';
/**
 * 将val限定在一定的范围之内
 */
function range(val: number, min: number, max: number) {
    return Math.max(Math.min(val, max), min);
}

/**
 *
 */
function round(val: number, min: number, step: number) {
    return min + Math.round((val - min) / step) * step;
}

interface RafOptions {
    /** 起始值 */
    start: number;
    /** 结束值 */
    end: number;
    /** 时长，秒 */
    duration: number;
    /** 变量值回调 */
    cb: any;
    /** 结束 */
    complete?: any;
    /** 取消 */
    cancel?: any;
}

const NORMAL: number = 0;
const ROLL_TO_SLOT: number = 1;

const handlers: any = [];
let autoplayHandler: number | null = null;
function raf(options: RafOptions) {
    if (isNaN(options.start) || isNaN(options.end) || isNaN(options.duration)) {
        return;
    }
    if (options.start === options.end) {
        if (options.complete) {
            options.complete();
        }
        return;
    }
    let willCancel = false;
    const startTime = new Date().valueOf();
    const endTime = startTime + options.duration * 1000;
    const r = () => {
        let now = new Date().valueOf();
        if (now > endTime) {
            now = endTime;
        }
        const rate = (now - startTime) / (endTime - startTime);
        options.cb(options.start + rate * (options.end - options.start));
        if (now >= endTime) {
            if (options.complete) {
                options.complete();
            }
        } else if (willCancel) {
            if (options.cancel) {
                options.cancel();
            }
        } else {
            requestAnimationFrame(r);
        }
    };
    const cancel = () => {
        willCancel = true;
    };
    requestAnimationFrame(r);
    return cancel;
}

@Component({
    name: 'Swiper',
})
export default class Swiper extends Vue {
    /** 每张图片的展示时间 */
    @Prop({ type: Number, default: 5000 }) public duration!: number;
    /** 默认从0开始 */
    @Prop({ type: Number, default: 0 }) public value!: number;
    /** 图片内容 */
    @Prop({ type: Array, default() { return []; } }) public images!: Banner[];
    /** 图片内容 */
    @Prop({ type: Boolean, default: false }) public autoplay!: boolean;
    /** 背景显示内容 */
    @Prop({ type: String, default: 'cover' }) public imageBackgroundSize!: string;

    /** 每张图片的宽度 */
    public width: number = 0;
    /** 移动的距离 */
    public translate: number = 0;
    /** 当前是否已经锚定 */
    public isMount: boolean = false;
    /** 定时器 */
    public handler: number | null = null;

    public status: number = NORMAL;

    public get formatImages() {
        const images = this.images;
        const length = images.length;
        if (length === 0) {
            return [];
        } else if (length === 1) {
            const image = images[0];
            return [ image, image, image ];
        } else if (length === 2) {
            return [ images[0], images[1], images[0], images[1] ];
        } else {
            return images;
        }
    }
    /** 右边界 */
    public get rangeMax() {
        return 0;
    }
    /** 左边界 */
    public get rangeMin() {
        return -this.width * (this.images.length - 1);
    }
    public get count() {
        return this.images.length;
    }

    @Watch('$props.value')
    public onValueChange(val: number) {
        const max = -this.width * val;
        const min = max - this.width;
        if (this.translate <= min || this.translate > max) {
            this.translate = range(-this.width * val, this.rangeMin, 0);
        }
    }
    @Watch('status')
    public onStatusChange(val: number) {
        const vm = this;
        if (val === NORMAL) {
            let handler = handlers.pop();
            while (handler) {
                handler();
                handler = handlers.pop();
            }
        }
    }

    /** 滑动到插槽位置 */
    public rollToSlot(index?: number) {
        const vm = this;
        const translate = isUndef(index) ? round(vm.translate, 0, vm.width) : (-index * vm.width);
        const duration = (Math.abs(translate - vm.translate) / vm.width) * 0.4;
        // 执行动画
        const handler = raf({
            start: vm.translate,
            end: translate,
            duration,
            cb: (result: number) => {
                vm.translate = result;
            },
            complete: () => {
                const rangeMin = -this.width * (this.images.length);
                vm.translate = (vm.translate + rangeMin) % (-rangeMin);
                // 更新index
                const index = ((-translate / vm.width) + this.images.length) % this.images.length;
                vm.$emit('input', index);
            },
        });
        handlers.push(handler);
    }
    public onTouchDown() {
        this.status = NORMAL;
        this.stopAutoplay();
    }
    /**
     * 移动触发
     */
    public onTouchMove({ x, y, startMoveCoordinate, moveCoordinate }: any) {
        if (startMoveCoordinate === 'x') {
            // 需要屏幕的宽度，限制在当前的swiper页中
            const translate = Math.max(Math.min(this.translate + x, -(this.value - 1) * this.width), -(this.value + 1) * this.width);
            this.translate = translate;
        }
    }
    public onTouchUp() {
        if (this.autoplay) {
            this.startAutoplay();
        }
    }
    /**
     * slide处理
     */
    public onTouchSlide() {
        this.status = ROLL_TO_SLOT;
        this.rollToSlot();
    }
    /**
     * fling处理
     */
    public onTouchFling({ speedX }: any) {
        this.status = ROLL_TO_SLOT;
        let index: number | null = null;
        // if (speedX > 0) {
        //     index = (this.value - 1 + this.images.length) % this.images.length;
        // } else {
        //     index = (this.value + 1) % this.images.length;
        // }
        if (speedX > 0) {
            index = (this.value - 1);
        } else {
            index = (this.value + 1);
        }
        this.rollToSlot(index);
    }
    /**
     * 启动autoplay
     */
    public startAutoplay() {
        autoplayHandler = window.setInterval(() => {
            this.$emit('input', (this.value + 1) % this.images.length);
        }, this.duration);
    }
    /**
     * 结束autoplay
     */
    public stopAutoplay() {
        if (autoplayHandler !== null) {
            window.clearInterval(autoplayHandler);
            autoplayHandler = null;
        }
    }

    public created() {
        if (this.autoplay) {
            this.startAutoplay();
        }
    }
    public updated() {
        const vm = this;
        if (!vm.width) {
            const $container = vm.$refs.swiperContainer as HTMLElement;

            if ($container && $container.getBoundingClientRect) {
                vm.width = $container.getBoundingClientRect().width;
            }

            // 设置默认开始位置
            vm.translate = -vm.value * vm.width;
        }
    }
    public mounted() {
        const vm = this;
        this.$nextTick(() => {
            const $container = vm.$refs.swiperContainer as HTMLElement;

            if ($container && $container.getBoundingClientRect) {
                vm.width = $container.getBoundingClientRect().width;
            }

            // 设置默认开始位置
            vm.translate = -vm.value * vm.width;
        });
    }
    public beforeDestroy() {
        this.stopAutoplay();
    }

    public render(h: typeof Vue.prototype.$createElement) {
        const index = this.value;
        const length = this.formatImages.length;
        const list = [ (index - 1 + length) % length, index, (index + 1) % length ];
        const slides = list.map((i1, i2) => {
            const item = this.formatImages[i1];
            return h('div', {
                class: 'ro-swiper-content-wrap ' + (this.status === ROLL_TO_SLOT ? 'transition' : ''),
                key: i1,
                style: {
                    transform: `translate(${this.translate + (index + i2 - 1) * this.width}px,0)`,
                },
            }, [
                h('div', {
                    class: 'ro-swiper-content',
                }, [
                    h('div', {
                        class: 'swiper-image',
                        style: {
                            'background-image': `url(${item.image || item.placeholder})`,
                            'background-size': this.imageBackgroundSize,
                        },
                        on: {
                            click: () => {
                                const url = item.url;
                                if (url) {
                                    if (url[0] === '/') {
                                        this.$router.push({
                                            path: url,
                                        });
                                    } else {
                                        location.href = url;
                                    }
                                }
                                this.$emit('select', item);
                            },
                        },
                    }),
                    h('img', {
                        class: 'swiper-image-item',
                        attrs: {
                            src: item.image || item.placeholder,
                        },
                        on: {
                            click: () => {
                                const url = item.url;
                                if (url) {
                                    if (url[0] === '/') {
                                        this.$router.push({
                                            path: url,
                                        });
                                    } else {
                                        location.href = url;
                                    }
                                }
                                this.$emit('select', item);
                            },
                        },
                    }),
                ]),
            ]);
        });

        // 创建touch
        const touch = h('Touch', {
            on: {
                'touch-down': this.onTouchDown,
                'touch-move': this.onTouchMove,
                'touch-up': this.onTouchUp,
                'touch-slide': this.onTouchSlide,
                'touch-fling': this.onTouchFling,
            },
            ref: 'touch',
        }, slides);

        const el = h('div', {
            class: [ 'ro-swiper-container', 'swiper' ],
            ref: 'swiperContainer',
        }, [ touch ]);
        return el;
    }
}
