import Vue from 'vue';
import { RootState } from '@/types/store';
import { Module } from 'vuex';
import PersonalInfo from '@/models/personalInfo';
import * as Sentry from '@sentry/browser';
import { decodeJwtPayload } from '@/lib/script/crypto';
import { get, set } from '@/lib/service/storage';
import User from '@/models/user';
export interface UserState {
    /** 用户基础信息 */
    basicInfo: User.BasicInfo | null;
    basicInfoMap: {
        [id: number]: User.BasicInfo | undefined,
    };
    stats: {
        total_distance: number | null,
        total_days: number | null,
        total_time: number | null,
    };
    /** 设置 */
    setting: {
        enable_sign_in_alert: boolean,
        sign_in_alert_time: string,
    } | null;
    /** 用户信息 */
    personalInfo: PersonalInfo | null;
    cachePersonalInfo: PersonalInfo | null;
    hasRecordNotify11: {
        area_code: string,
        mobile: string,
    } | null;
}

const m: Module<UserState, RootState> = {
    namespaced: true,
    state: {
        basicInfo: null,
        basicInfoMap: {},
        stats: {
            total_distance: null,
            total_days: null,
            total_time: null,
        },
        setting: null,
        personalInfo: null,
        cachePersonalInfo: null,
        hasRecordNotify11: null,
    },
    mutations: {
        /**
         * 记录用户基础信息
         */
        setBasicInfo(state, basicInfo) {
            state.basicInfo = basicInfo;
            if (basicInfo) {
                Sentry.configureScope((scope) => {
                    scope.setUser({id: basicInfo.id});
                });
                gio('setUserId', basicInfo.id);
                gio('people.set', 'mobile_bound', basicInfo.mobile_bound ? '1' : '0');
            }
        },
        /**
         * 记录用户基础信息
         */
        setBasicInfoMap(state, { id, info }) {
            Vue.set(state.basicInfoMap, id, info);
        },
        /**
         * 记录数据
         */
        setStats(state, stats) {
            state.stats = stats;
        },
        /**
         * 记录setting
         */
        setSetting(state, setting) {
            state.setting = setting;
        },
        /**
         * 记录personalInfo
         */
        setPersonalInfo(state, personalInfo) {
            state.personalInfo = personalInfo;
        },
        /**
         * 记录personalInfo
         */
        setCachePersonalInfo(state, personalInfo) {
            state.cachePersonalInfo = personalInfo;
        },
        /**
         * 记录通知11上线
         */
        setRecordNotify11(state, notify11) {
            state.hasRecordNotify11 = notify11;
        },
    },
    actions: {
        /**
         * 获取用户基本数据
         * @param id 如果不传则获取自己的数据
         */
        async getBasicInfo({ state, commit, rootState }, { id, forceUpdate } = {}) {
            // 已有缓存
            if (((!id && state.basicInfo) || (id && state.basicInfoMap[id])) && !forceUpdate) {
                return;
            }

            const user = new User();
            user.forceUpdate = !!forceUpdate;
            if (id) {
                user.vector.id = id;
            } else {
                const jwt = rootState.auth.jwt;
                if (jwt) {
                    const parsedJwt = decodeJwtPayload(jwt);
                    if (parsedJwt) {
                        const serialNumber = parsedJwt.uid;
                        user.vector.serial_number = serialNumber;
                    }
                }
            }

            await user.get();
        },
        async getStats({ state, commit }) {
            const response = await Vue.axios.get('/api/v1/user/stats');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setStats', rdata.data);
            }
            return rdata;
        },
        /**
         * 获取设置
         */
        async getSetting({ state, commit }) {
            const response = await Vue.axios.get('/api/v1/user/settings');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setSetting', rdata.data);
            }
            return rdata;
        },
        /**
         * 更新设置
         */
        async updateSetting({ state, commit }, { key, value }) {
            const response = await Vue.axios.post('/api/v1/user/settings', {
                key,
                value,
            });
            return response.data;
        },
        /**
         * 实名认证申请
         */
        async initRealNameVerify({ state, commit }, { name, idCard, mobile }) {
            const response = await Vue.axios.post('/api/v1/user/init_real_name_verify', {
                name,
                id_card: idCard,
                mobile,
            });
            return response.data;
        },
        /**
         * 实名认证确认
         */
        async checkRealNameVerify({ dispatch }, { token, code }) {
            const response = await Vue.axios.post('/api/v1/user/check_real_name_verify', {
                token,
                code,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                await dispatch({
                    type: 'user/getBasicInfo',
                    forceUpdate: true,
                });
            }
            return rdata;
        },
        /**
         * 登录验证码
         */
        async getVerificateCode({ state, commit }, { areaCode, mobile }) {
            const response = await Vue.axios.post('/api/v1/user/bind_mobile', {
                area_code: areaCode,
                mobile,
            });
            return response.data;
        },
        /**
         * 绑定手机
         */
        async bindingPhone({ state, commit }, { serialNumber, code }) {
            const response = await Vue.axios.post('/api/v1/user/confirm_mobile', {
                serial_number: serialNumber,
                code,
            });
            return response.data;
        },
        /**
         * 绑定邀请人
         */
        async recordReferrer({ state, commit }, referrer) {
            const response = await Vue.axios.post('/api/v1/user/record_referrer', {
                user_id: referrer,
            });
            return response.data;
        },
        /**
         * 获取用户信息
         */
        async getPersonalInfo({ state, commit }, { forceUpdate } = {}) {
            if (state.personalInfo && state.cachePersonalInfo && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/user/user_info');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setPersonalInfo', new PersonalInfo(rdata.data));
                commit('setCachePersonalInfo', new PersonalInfo(rdata.data));
            }
            return rdata;
        },
        /**
         * 获取用户信息
         * @param activity_id 选填
         */
        async updatePersonalInfo({ state, commit, dispatch }, { activityId, forceUpdate } = {}) {
            dispatch('getPersonalInfo');
            const response = await Vue.axios.post('/api/v1/user/user_info', {
                activity_id: activityId,
                ...state.cachePersonalInfo!.getSnake(),
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                state.personalInfo!.inflate(state.cachePersonalInfo!);
            }
            return rdata;
        },
        /**
         * 记录通知11届
         */
        async recordNotify11({ state, commit }, { mobile, areaCode, mobileBound, requireBound }) {
            const response = await Vue.axios.post('/api/v1/user/record_notify_11', {
                mobile,
                area_code: areaCode,
                mobile_bound: mobileBound,
                require_bound: requireBound,
            });
            const rdata = response.data;
            return rdata;
        },
        async getRecordNotify11({ state, commit }) {
            const response = await Vue.axios.get('/api/v1/user/record_notify_11');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setRecordNotify11', rdata.data);
            }
            return rdata;
        },
    },
};

export default m;
