const CONFIRM_TEXT = '确定';
const CANCEL_TEXT = '取消';
import Popup from './popup';
import { Component, Vue, Prop, Mixins, Watch } from 'vue-property-decorator';

// 不改为.vue文件，为了保证MessageBoxView的属性能够被正确识别
@Component({
    name: 'MessageBoxView',
    template: `<div class="mint-msgbox-wrapper" :class="[theme]">
        <transition name="msgbox-bounce">
            <div class="mint-msgbox" v-show="value">
                <div class="mint-msgbox-header" v-if="title !== ''">
                    <div class="mint-msgbox-title">{{ title }}</div>
                </div>
                <div class="mint-msgbox-content" v-if="message !== ''">
                    <div class="mint-msgbox-message" v-html="message"></div>
                    <div class="mint-msgbox-input" v-show="showInput">
                        <input v-model="inputValue" :placeholder="inputPlaceholder" ref="input">
                        <div class="mint-msgbox-errormsg"
                            :style="{ visibility: !!editorErrorMessage ? 'visible' : 'hidden' }">
                            {{ editorErrorMessage }}
                        </div>
                    </div>
                </div>
                <div class="mint-msgbox-btns">
                    <button :class="[ cancelButtonClasses ]" v-show="showCancelButton"
                        @click="handleAction('cancel')">
                        {{ cancelButtonText }}
                    </button>
                    <button :class="[ confirmButtonClasses ]" v-show="showConfirmButton"
                        @click="handleAction('confirm')">
                        {{ confirmButtonText }}
                    </button>
                </div>
            </div>
        </transition>
    </div>`,
})
export default class MessageBoxView extends Mixins(Popup) {

    @Prop({ default: true }) public modal!: boolean;
    @Prop({ type: Boolean, default: true }) public showClose!: boolean;
    @Prop({ type: Boolean, default: false }) public lockScroll!: boolean;
    @Prop({ default: true }) public closeOnClickModal!: boolean;
    @Prop({ default: true }) public closeOnPressEscape!: boolean;
    @Prop({ type: String, default: 'text' }) public inputType!: string;

    public title: string = '';
    public message: string = '';
    public type: string = '';
    /** 风格，最终将是一个class */
    public theme: string = '';
    public showInput: boolean = false;
    public inputValue: any = null;
    public inputPlaceholder: string = '';
    public inputPattern: any = null;
    public inputValidator: any = null;
    public inputErrorMessage: string = '';
    public showConfirmButton: boolean = true;
    public showCancelButton: boolean = false;
    public confirmButtonText: string = CONFIRM_TEXT;
    public cancelButtonText: string = CANCEL_TEXT;
    public confirmButtonClass: string = '';
    public confirmButtonDisabled: boolean = false;
    public cancelButtonClass: string = '';
    public editorErrorMessage: any = null;
    public callback: any = null;

    public get confirmButtonClasses() {
        let classes = 'mint-msgbox-btn mint-msgbox-confirm ' + this.confirmButtonClass;
        // @ts-ignore
        if (this.confirmButtonHighlight) {
            classes += ' mint-msgbox-confirm-highlight';
        }
        return classes;
    }
    public get cancelButtonClasses() {
        let classes = 'mint-msgbox-btn mint-msgbox-cancel ' + this.cancelButtonClass;
        // @ts-ignore
        if (this.cancelButtonHighlight) {
            classes += ' mint-msgbox-cancel-highlight';
        }
        return classes;
    }

    @Watch('inputValue')
    public onInputValueChange() {
        // @ts-ignore
        if (this.$type === 'prompt') {
            this.validate();
        }
    }
    @Watch('value')
    // @ts-ignore
    public onValueChange(val) {
        this.handleInputType(this.inputType);
        // @ts-ignore
        if (val && this.$type === 'prompt') {
            setTimeout(() => {
                if (this.$refs.input) {
                    // @ts-ignore
                    this.$refs.input.focus();
                }
            }, 500);
        }
    }
    @Watch('inputType')
    // @ts-ignore
    public onInputTypeChange(val) {
        this.handleInputType(val);
    }

    public doClose() {
        this.value = false;
        this._closing = true;
        if (this.onClose) {
            this.onClose();
        }
        setTimeout(() => {
            if (this.modal && this.bodyOverflow !== 'hidden') {
                document.body.style.overflow = this.bodyOverflow!;
                document.body.style.paddingRight = this.bodyPaddingRight!;
            }
            this.bodyOverflow = null;
            this.bodyPaddingRight = null;
        }, 200);
        this.opened = false;
        if (!this.transition) {
            this.doAfterClose();
        }
    }
    // @ts-ignore
    public handleAction(action) {
        // @ts-ignore
        if (this.$type === 'prompt' && action === 'confirm' && !this.validate()) {
            return;
        }
        const callback = this.callback;
        this.value = false;
        callback(action);
    }
    public validate() {
        // @ts-ignore
        if (this.$type === 'prompt') {
            const inputPattern = this.inputPattern;
            if (inputPattern && !inputPattern.test(this.inputValue || '')) {
                this.editorErrorMessage = this.inputErrorMessage || '输入的数据不合法!';
                // @ts-ignore
                this.$refs.input.classList.add('invalid');
                return false;
            }
            const inputValidator = this.inputValidator;
            if (typeof inputValidator === 'function') {
                const validateResult = inputValidator(this.inputValue);
                if (validateResult === false) {
                    this.editorErrorMessage = this.inputErrorMessage || '输入的数据不合法!';
                    // @ts-ignore
                    this.$refs.input.classList.add('invalid');
                    return false;
                }
                if (typeof validateResult === 'string') {
                    this.editorErrorMessage = validateResult;
                    return false;
                }
            }
        }
        this.editorErrorMessage = '';
        // @ts-ignore
        this.$refs.input.classList.remove('invalid');
        return true;
    }
    // @ts-ignore
    public handleInputType(val) {
        if (val === 'range' || !this.$refs.input) {
            return;
        }
        // @ts-ignore
        this.$refs.input.type = val;
    }
}
