import Vue from 'vue';
import store from '@/store/index';
import { set, get, setex } from '@/lib/service/storage';
import { dateNow, dateFormat, dateOffset } from '@/lib/utils';
export default class User {
    /** 原始数据 */
    public data: User.BasicInfo | undefined;
    /** 向量，没有id默认获取自己的数据 */
    public vector: {
        id?: number,
        serial_number?: number,
    } = {};
    /** 是否强制更新 */
    public forceUpdate: boolean = false;

    public async get() {
        if (!this.forceUpdate) {
            await this.getFromWebStorage();
        }
        if (this.data) {
            this.setStore();
            window.setTimeout(() => {
                this.getFromServer();
            }, 10000);
        } else {
            await this.getFromServer();
        }
    }

    /**
     * 从服务器获取数据
     */
    public async getFromServer() {
        const response = await Vue.axios.get('/api/v1/user/basic_info', {
            params: {
                id: this.vector.id,
            },
        });
        const rdata = response.data;
        if (rdata.status === 0) {
            this.data = rdata.data;
            this.setStore();
            this.setWebStorage();
        }
    }
    /**
     * 从webstorage获取数据
     */
    public async getFromWebStorage() {
        let raw: any = null;
        if (this.vector.id) {
            raw = get(`user_id_${this.vector.id}`);
        } else if (this.vector.serial_number) {
            raw = get(`user_sn_${this.vector.serial_number}`);
        }
        if (raw) {
            this.data = JSON.parse(raw);
        }
    }
    /**
     * 更新store数据
     */
    public async setStore() {
        if (!this.data) {
            return;
        }
        // 更新内存
        const parsedJwt = store.state.auth.parsedJwt;
        if (!this.vector.id || (parsedJwt && parsedJwt.uid === this.vector.serial_number)) {
            store.commit('user/setBasicInfo', this.data);
        }
        store.commit('user/setBasicInfoMap', {
            id: this.data.id,
            info: this.data,
        });
    }
    /**
     * 更新webStorage数据
     */
    public async setWebStorage() {
        if (!this.data) {
            return;
        }
        setex(`user_id_${this.data.id}`, this.data, dateFormat(dateOffset(dateNow(), 3), 'Y-m-d H:i:s'));
        setex(`user_sn_${this.data.serial_number}`, this.data, dateFormat(dateOffset(dateNow(), 3), 'Y-m-d H:i:s'));
    }
}
