import Vue from 'vue';
import App from './App.vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import router from './router/index';
import store from './store/index';
import Uploader from '@forzoom/uploader';
import MobileButton from '@forzoom/mobile-button';
import jssdk from '@forzoom/jssdk';
import {
    Popup,
} from '@forzoom/popup';
import Touch from '@forzoom/touch';
import VueSkeleton from '@/lib/vue/plugins/skeleton/index'; // 暂时不知道是什么
import VueLazyload from 'vue-lazyload';
import Thumbnail from '@forzoom/thumbnail';
import VueRouteScroll from '@forzoom/vue-router-scroll';
import ScrollListener from '@forzoom/scroll-listener';
import ListSection from '@/components/list/listSection.vue';
import ListTitle from '@/components/list/listTitle.vue';
import ListItem from '@/components/list/listItem.vue';
import ListFooter from '@/components/list/listFooter.vue';
import SnakeSpinner from '@/components/spinner/snake.vue';
import DaisySpinner from '@/components/spinner/daisy.vue';
import SpinnerMobileButton from '@/components/button/spinnerMobileButton.vue';
import ROSwitch from '@/components/switch/switch.vue';
import PreviewImageList from '@/components/extra/previewImageList.vue';
import PreviewImage from '@/components/extra/previewImage.vue';
import PreviewBg from '@/components/extra/previewBg.vue';
import ROSwiper from '@/components/swiper/swiper.vue';
import ROSwiperIndicator from '@/components/swiper/swiperIndicator.vue';
import {
    swiper,
    swiperSlide,
} from 'vue-awesome-swiper';
import SlideNav from '@/components/nav/slideNav.vue';
import CommonNav from '@/components/nav/commonNav.vue';
import Brand from '@/features/activity/brand.vue';
import 'lib-flexible';
import 'normalize.css';
// 引入装饰器
import { Component } from 'vue-property-decorator';
// 错误捕获
import './lib/service/sentry';

window.apiUrl = process.env.RO_API_URL;
window.pageUrl = process.env.RO_PAGE_URL;
window.appPageRoot = process.env.RO_APP_PAGE_ROOT;
// 注册钩子
Component.registerHooks([
    'beforeRouteEnter',
    'beforeRouteUpdate',
    'beforeRouteLeave',
]);
// 全局混入
Vue.mixin({
    beforeRouteLeave(to: Route, from: Route, next: (to?: any) => any) {
        if (to.meta && !to.meta.disableShare) {
            // 分享
            enableShareHook(from);
        }
        next();
    },
});
// 引入工具
import {
    dateParse,
    dateFormat,
    placeholder,
    currency,
    isUndef,
    dateNow,
    securityText,
    overlongTextProcessing,
} from '@/lib/utils';
import { enableShareHook } from './router/hook';
import { Route } from 'vue-router';
// 提示框
import Toast from './components/toast/toast';
import { del } from './lib/service/storage';
import { KEY_AUTH_TOKEN } from './lib/constant';

const imageFilter = Thumbnail.generateImageFilter({
    bed: {
        aliyun: 'https://static.run100.runorout.cn',
        default: 'https://static.run100.runorout.cn',
    },
    isAliyun(listener, options) {
        return /static.run100.runorout.cn/.test(listener.src);
    },
});

// 全局混入
Vue.mixin({
    mounted() {
        if (history && history.length) {
            store.commit('setHistoryLength', history.length);
        }
    },
});

// 全局注册组件
Vue.component('MobileButton', MobileButton);
Vue.component('ListTitle', ListTitle);
Vue.component('ListSection', ListSection);
Vue.component('ListItem', ListItem);
Vue.component('ListFooter', ListFooter);
Vue.component('SnakeSpinner', SnakeSpinner);
Vue.component('DaisySpinner', DaisySpinner);
Vue.component('Popup', Popup);
Vue.component('MyPopup', Popup);
Vue.component('Touch', Touch);
Vue.component('SpinnerMobileButton', SpinnerMobileButton);
Vue.component('ROSwitch', ROSwitch);
Vue.component('SlideNav', SlideNav);
Vue.component('CommonNav', CommonNav);
Vue.component('Brand', Brand);
Vue.component('PreviewImageList', PreviewImageList);
Vue.component('PreviewImage', PreviewImage);
Vue.component('PreviewBg', PreviewBg);
Vue.component('swiper', swiper);
Vue.component('swiperSlide', swiperSlide);
Vue.component('ROSwiper', ROSwiper);
Vue.component('ROSwiperIndicator', ROSwiperIndicator);
// 注册全局过滤器
Vue.filter('dateParse', dateParse);
Vue.filter('dateFormat', dateFormat);
Vue.filter('placeholder', placeholder);
Vue.filter('currency', currency);
Vue.filter('securityText', securityText);
Vue.filter('dateDisplay', (val: string) => {
    if (isUndef(val)) {
        return val;
    }
    const date = dateParse(val);
    const now = dateNow();
    const interval = (now.valueOf() - date.valueOf()) / 60000; // 分钟数
    if (interval <= 1) {
        return '刚刚';
    } else if (interval < 60) {
        return Math.floor(interval) + '分钟前';
    } else if (interval < 60 * 24) {
        return Math.floor(interval / 60) + '小时前';
    } else if (interval < 60 * 24 * 3) {
        return Math.floor(interval / 60 / 24) + '天前';
    } else {
        return dateFormat(date);
    }
});
Vue.filter('overlongTextProcessing', overlongTextProcessing);
// 定义全局指令
Vue.directive('focus', {
    inserted(el) {
        if (el.dataset.focus && el.dataset.focus === 'true') {
            el.focus();
        }
    },
});
// 创建axios实例
const axiosInstance = axios.create({
    // 设置一个 `baseURL` 便于为 axios 实例的方法传递相对 URL
    baseURL: window.apiUrl,
    // 表示跨域请求时是否需要使用凭证
    withCredentials: true,
    // 超时时间
    timeout: 30000,
    // 有效状态码
    validateStatus(status) {
        return status >= 200 && status < 500;
    },
});
// 响应拦截器
axiosInstance.interceptors.response.use((response) => {
    // 发生401
    if (response.status === 401) {
        del(KEY_AUTH_TOKEN);
        location.reload();
    }
    return response;
}, (error) => {
    // 连接超时响应
    const message = error.message as string;
    if (message && message.indexOf('timeout') >= 0) {
        store.commit('startError', {
            type: 'icon',
            icon: '&#xe613;',
            text: '当前网络环境较差',
        });
    }
    return Promise.reject(error);
});
// 同步获取服务器时间
axiosInstance.interceptors.response.use(function syncServerTime(response) {
    if (response && response.data && response.data.timestamp) {
        store.commit('setServerTimestamp', response.data.timestamp);
    }
    return response;
});
// 使用插件
Vue.use(VueAxios, axiosInstance);
Vue.use(Uploader, {
    transformWXLocalImageData: true,
});
Vue.use(jssdk, {
    store,
});
Vue.use(VueSkeleton);
Vue.use(VueLazyload, {
    filter: imageFilter,
});
Vue.use(ScrollListener, {
    store,
});
Vue.use(Thumbnail, {
    imageFilter,
    enterClass: 'image-blur1',
    leaveClass: 'image-blur2',
});
Vue.use(VueRouteScroll, {
    router,
});
// 非生产环境，使用mock数据
if (process.env.NODE_ENV !== 'production') {
    require('@/mocks/index.js');
}
// 阻止启动生产消息（常用作指令）
Vue.config.productionTip = false;
Vue.config.keyCodes = {
    enter: 13,
};

new Vue({
    router,
    store,
    render: (h) => h(App),
}).$mount('#app');
