import {
    ROUTE_NAME,
    URL_MALL,
} from '@/lib/constant';
import store from '@/store';
import { Route } from 'vue-router';
import url from 'url';

export default [
    // 成为队长咨询
    {
        path: '/consult',
        name: ROUTE_NAME.CONSULT,
        meta: {
            title: '咨询',
        },
        component: () => import('@/pages/extra/consult.vue'),
    },
    // 当前报名的百日跑
    {
        path: '/activity/current',
        name: ROUTE_NAME.ACTIVITY_CURRENT,
        meta: {
            title: '',
        },
        redirect() {
            return {
                name: ROUTE_NAME.ACTIVITY,
                params: {
                    activityId: '1',
                },
            };
        },
    },
    {
        path: '/mall',
        name: ROUTE_NAME.MALL,
        meta: {
            title: '跳转中',
        },
        async beforeEnter(to: Route, from: Route, next: any) {
            // 用户基础信息
            const userBasicInfo = store.state.user.basicInfo;
            if (!userBasicInfo) {
                return;
            }
            if (!userBasicInfo.mobile_bound) {
                location.href = URL_MALL + '/index';
            } else {
                // 获取jwt
                store.commit('startRequest');
                const rdata = await store.dispatch('getStoreJwt');
                store.commit('endRequest');
                if (rdata.status !== 0) {
                    next(false);
                    return;
                }
                const query = to.query;
                if (query && query.path) {
                    const result = url.parse(query.path as string, true);
                    result.query.jwt = rdata.data;
                    result.search = undefined;
                    location.href = URL_MALL + url.format(result);
                } else {
                    const data = rdata.data;
                    location.href = URL_MALL + '/index?loginInfo=' + encodeURIComponent(JSON.stringify(data));
                }
            }
        },
    },
];
