const CONFIRM_TEXT = '确定';
const CANCEL_TEXT = '取消';

interface Msg {
    options: MessageBoxOption;
    callback: any;
    resolve?: any;
    reject?: any;
}
interface MessageBoxOption {
    /** 标题 */
    title?: string;
    /** 内容 */
    message?: string;
    /** 类型 */
    type?: string;
    /** 风格 */
    theme?: string;
    /** 是否显示input */
    showInput?: boolean;
    showClose?: boolean;
    modalFade?: boolean;
    lockScroll?: boolean;
    closeOnClickModal?: boolean;
    inputValue?: null;
    inputPlaceholder?: string;
    inputPattern?: null;
    inputValidator?: null;
    inputErrorMessage?: string;
    showConfirmButton?: boolean;
    showCancelButton?: boolean;
    confirmButtonPosition?: 'right';
    confirmButtonHighlight?: boolean;
    cancelButtonHighlight?: boolean;
    confirmButtonText?: string;
    cancelButtonText?: string;
    confirmButtonClass?: string;
    cancelButtonClass?: string;

    callback?: any;

    /** 用于获取一个函数用于close */
    getCloseHandler?: any;
    /** 将传递给popup */
    onClose?: any;

    // 类型 prompt confirm alert
    $type?: string;
}

const defaults = {
    title: '提示',
    message: '',
    type: '',
    theme: 'default',
    showInput: false,
    showClose: true,
    modalFade: false,
    lockScroll: false,
    closeOnClickModal: true,
    inputValue: null,
    inputPlaceholder: '',
    inputPattern: null,
    inputValidator: null,
    inputErrorMessage: '',
    showConfirmButton: true,
    showCancelButton: false,
    confirmButtonPosition: 'right',
    confirmButtonHighlight: false,
    cancelButtonHighlight: false,
    confirmButtonText: CONFIRM_TEXT,
    cancelButtonText: CANCEL_TEXT,
    confirmButtonClass: '',
    cancelButtonClass: '',
};

import Vue from 'vue';
import msgboxVue from './messageBoxView';
import { CombinedVueInstance } from 'vue/types/vue';

const merge = (target: any, ...args: any[]) => {
    for (let i = 0, j = args.length; i < j; i++) {
        const source = args[i];
        for (const prop in source) {
            if (source.hasOwnProperty(prop)) {
                const value = source[prop];
                if (value !== undefined) {
                    target[prop] = value;
                }
            }
        }
    }

    return target;
};

const MessageBoxConstructor = Vue.extend(msgboxVue);

let currentMsg: Msg;
// 这里的msgboxVue确实extend了Vue，但是由于单文件组件的问题，导入的.vue文件都被认为是Vue(根据shims-vue.d.ts)
let instance: CombinedVueInstance<msgboxVue, object, object, object, Record<keyof object, any>>;
let msgQueue: Msg[] = [];

const defaultCallback = (action: string) => {
    if (currentMsg) {
        const callback = currentMsg.callback;
        if (typeof callback === 'function') {
            if (instance.showInput) {
                callback(instance.inputValue, action);
            } else {
                callback(action);
            }
        }
        if (currentMsg.resolve) {
            const $type = currentMsg.options.$type;
            if ($type === 'confirm' || $type === 'prompt') {
                if (action === 'confirm') {
                    if (instance.showInput) {
                        currentMsg.resolve({ value: instance.inputValue, action });
                    } else {
                        currentMsg.resolve(action);
                    }
                } else if (action === 'cancel' && currentMsg.reject) {
                    currentMsg.reject(action);
                }
            } else {
                currentMsg.resolve(action);
            }
        }
    }
};

const initInstance = () => {
    // 这里返回的只能是Instance(Vue)
    // @ts-ignore
    instance = new MessageBoxConstructor({
        el: document.createElement('div'),
    });

    instance.callback = defaultCallback;
};

// 触发一次显示
const showNextMsg = () => {
    if (!instance) {
        initInstance();
    }
    if (!instance.value || instance.closeTimer) {
        if (msgQueue.length > 0) {
            currentMsg = msgQueue.shift() as Msg;

            const options = currentMsg.options;
            for (const prop in options) {
                if (options.hasOwnProperty(prop)) {
                    // @ts-ignore
                    instance[prop] = options[prop];
                }
            }
            if (options.callback === undefined) {
                instance.callback = defaultCallback;
            }
            ['modal', 'showClose', 'closeOnClickModal', 'closeOnPressEscape'].forEach((prop) => {
                // @ts-ignore
                if (instance[prop] === undefined) {
                    // @ts-ignore
                    instance[prop] = true;
                }
            });
            document.body.appendChild(instance.$el);
            if (options.getCloseHandler) {
                options.getCloseHandler(() => {
                    if (instance) {
                        instance.value = false;
                    }
                });
            }

            Vue.nextTick(() => {
                instance.value = true;
            });
        }
    }
};

// @ts-ignore
const MessageBox = (options: MessageBoxOption | string, callback?, type?) => {
    if (typeof options === 'string') {
        options = {
            title: options,
        };
        if (callback) {
            options.message = callback;
        }
        if (type) {
            options.type = type;
        }
    } else if (options.callback && !callback) {
        callback = options.callback;
    }

    if (typeof Promise !== 'undefined') {
        return new Promise((resolve, reject) => { // eslint-disable-line
            msgQueue.push({
                // @ts-ignore
                options: merge({}, defaults, MessageBox.defaults || {}, options),
                callback,
                resolve,
                reject,
            });
            showNextMsg();
        });
    } else {
        msgQueue.push({
            // @ts-ignore
            options: merge({}, defaults, MessageBox.defaults || {}, options),
            callback,
        });

        showNextMsg();
    }
};

// 提供给应用层使用
// @ts-ignore
MessageBox.setDefaults = (defaults) => {
    // @ts-ignore
    MessageBox.defaults = defaults;
};

// @ts-ignore
MessageBox.alert = (message: string, title: string, options: MessageBoxOption) => {
    if (typeof title === 'object') {
        options = title;
        title = '';
    }
    return MessageBox(merge({
        title,
        message,
        $type: 'alert',
        closeOnPressEscape: false,
        closeOnClickModal: false,
    }, options));
};

// @ts-ignore
MessageBox.confirm = (message: string, title: string, options: MessageBoxOption) => {
    if (typeof title === 'object') {
        options = title;
        title = '';
    }
    return MessageBox(merge({
        title,
        message,
        $type: 'confirm',
        showCancelButton: true,
    }, options));
};

// @ts-ignore
MessageBox.prompt = (message: string, title: string, options: MessageBoxOption) => {
    if (typeof title === 'object') {
        options = title;
        title = '';
    }
    return MessageBox(merge({
        title,
        message,
        showCancelButton: true,
        showInput: true,
        $type: 'prompt',
    }, options));
};

// @ts-ignore
MessageBox.close = () => {
    if (!instance) {
        return;
    }
    instance.value = false;
    msgQueue = [];
    // @ts-ignore
    currentMsg = null;
};

export default MessageBox;
export { MessageBox };
