import Schema, { Model } from '@forzoom/models';
import { RuntimeType } from '.';

@RuntimeType({
    models: {
        nickname: { type: 'String?', default: '' },
        avatar: { type: 'String?', default: '' },
        avatarUrl: { type: 'String?', default: '' },
        name: { type: 'String?', default: '' },
        gender: { type: 'String?', default: '' },
        age: { type: 'String?', default: '' },
        mobile: { type: 'String?', default: '' },
        district1Id: { type: 'Number?' },
        district2Id: { type: 'Number?' },
        district3Id: { type: 'Number?' },
        address: { type: 'String?', default: '' },
    },
})
export default class PersonalInfo extends Schema {
    public static models?: Model;
    /** 昵称 */
    public nickname!: string | null;
    /** 头像 */
    public avatar!: string | null;
    /** 头像url */
    public avatarUrl!: string | null;
    /** 活动id */
    public name!: string | null;
    /** 封面id */
    public gender!: string | null;
    /** 封面url */
    public age!: string | null;
    /** 名称 */
    public mobile!: string | null;
    /** 城市id */
    public district1Id!: number | null;
    /** 城市 */
    public district2Id!: number | null;
    /** 组别id */
    public district3Id!: number | null;
    /** 描述 */
    public address!: string | null;

    constructor(data?: object) {
        super(PersonalInfo.models!, data);
        this._name = PersonalInfo.name;
    }
}
