import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import {
    camelCase,
    isUndef,
    makeMap,
} from '@/lib/utils';

import UserInfo from '@/models/userInfo';

export interface EquipmentState {
    /** activityId => userInfo */
    mapUserInfo: {
        [activityId: string]: UserInfo | undefined,
    };
    /** activityId => userInfo */
    mapCacheUserInfo: {
        [activityId: string]: UserInfo | undefined,
    };
    originUserInfoMap: {
        [activityId: string]: any,
    };
    /** 存储从userInfo中获取的gift数据 */
    giftMap: {
        [giftOrderId: number]: Equipment.GiftItem,
    };
}
const m: Module<EquipmentState, RootState> = {
    namespaced: true,
    state: {
        mapUserInfo: {},
        mapCacheUserInfo: {},
        originUserInfoMap: {},
        giftMap: {},
    },
    mutations: {
        /**
         * 记录用户信息
         */
        setUserInfo(state, { activityId, userInfo }) {
            Vue.set(state.mapUserInfo, activityId, userInfo);
        },
        /**
         * 记录缓存用户信息
         */
        setCacheUserInfo(state, { activityId, userInfo }) {
            Vue.set(state.mapCacheUserInfo, activityId, userInfo);
        },
        /**
         * 存储开始
         */
        setOriginUserInfo(state, { activityId, info }: { activityId: string, info: any}) {
            Vue.set(state.originUserInfoMap, activityId, info);
            // tip: 这里的gift.id是giftOrder的id
            for (const gift of info.gifts) {
                Vue.set(state.giftMap, gift.id, gift);
            }
        },
    },
    actions: {
        /**
         * 获取用户信息
         *
         * @param {} forceUpdate 是否强制更新
         *
         * @return {} userInfo返回userInfo信息
         */
        async getUserInfo(
            { state, dispatch, commit },
            { activityId,  forceUpdate = false }:
            { activityId: string, forceUpdate: boolean }) {
            const userInfo = state.mapUserInfo[activityId];
            if (userInfo && !forceUpdate) {
                return;
            }
            if (forceUpdate) {
                state.mapUserInfo[activityId] = undefined;
                state.mapCacheUserInfo[activityId] = undefined;
            }

            const response = await Vue.axios.get('/api/v1/gift/user_info', {
                params: {
                    activity_id: activityId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                dispatch('userInfoInflateData', {
                    activityId,
                    data: rdata.data,
                });
                commit('setOriginUserInfo', {
                    activityId,
                    info: rdata.data,
                });
            }
        },
        /**
         * 填充数据
         */
        userInfoInflateData({ state, commit }, { activityId, data }: { activityId: string, data: any} ) {
            const tempData = data || {};
            const cacheUserInfo = new UserInfo({});
            const userInfo = new UserInfo({});
            const canModify = {
                can_modify: false,
            };
            Object.assign(tempData, canModify);
            // 默认不允许修改
            tempData.can_modify = isUndef(tempData.can_change_address) ? false : tempData.can_change_address;
            if (data && data.gifts) {
                const userInfoExtra = data.gifts;
                userInfoExtra.forEach((gift: Equipment.GiftItem) => {
                    let property = null;
                    if (tempData.can_modify === false && gift.can_change === true) {
                        tempData.can_modify = true;
                    }
                    const options: Equipment.OptionItem = {
                        giftId: gift.id,
                        all: [],
                        map: {},
                        title: gift.name,
                        size_table: gift.size_table,
                        change_spec_days: gift.change_spec_days,
                    };
                    gift.choices.forEach((item: Equipment.ChoiceItem) => {
                        options.all.push(item.id);
                        options.map[item.id] = item;
                    });
                    // gift相关的内容被放在了extra内容中
                    property = {
                        type: 'String',
                        default: gift.specification,
                        extra() {
                            return {
                                type: 'Size',
                                title: gift.name,
                                options,
                                display: false,
                                readonly: !gift.can_change,
                                size_table: gift.size_table,
                                confirm_word: gift.confirm_word,
                            };
                        },
                    };
                    if (property) {
                        const camel = camelCase(String(gift.id));
                        userInfo.registerProperty(camel, property);
                        // @ts-ignore
                        Vue.util.defineReactive(userInfo, camel);
                        cacheUserInfo.registerProperty(camel, property);
                        // @ts-ignore
                        Vue.util.defineReactive(cacheUserInfo, camel);
                    }
                });
            }
            cacheUserInfo.inflate(tempData);
            userInfo.inflate(tempData);
            commit('setUserInfo', { activityId, userInfo });
            commit('setCacheUserInfo', { activityId, userInfo: cacheUserInfo });
        },
        /**
         * 更新userInfo
         *
         * @param {} userInfo 所有的userInfo，允许传入null以覆盖值
         *  - name
         *  - gender
         *  - age
         *  - mobile
         *  - districtLevel1
         *  - districtLevel2
         *  - districtLevel3
         *  - addressDetail
         *  - clothesSize
         *  - app
         *  - knowFrom
         *  - shoeSize
         *
         * @return {} rdata，完整的JSON响应内容
         */
        async updateUserInfo({ state, commit }, { activityId }: { activityId: string }) {
            const origin = state.originUserInfoMap[activityId];
            const cacheUserInfo = state.mapCacheUserInfo[activityId];
            const userInfo = state.mapUserInfo[activityId];
            if (!cacheUserInfo || !userInfo) {
                return;
            }
            const keys = Object.keys(cacheUserInfo!) as Array<keyof UserInfo>;
            const specifications: {
                [id: string]: any,
            } = {};
            const gifts = origin && origin.gifts ? makeMap(origin.gifts, 'id') : {};
            for (const key of keys) {
                if (gifts[key]) {
                    specifications[key as string] = cacheUserInfo[key];
                }
            }
            const response = await Vue.axios.post('/api/v1/gift/user_info', {
                activity_id: activityId,
                specifications,
                ...cacheUserInfo.getSnake(),
                ...cacheUserInfo.getCamel(),
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                userInfo.inflate(cacheUserInfo);
            }
            return rdata;
        },
    },
};
export default m;
