import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import { isUndef } from '@/lib/utils';

export interface TeamState {
    /** 战队成员 */
    members: {
        [teamId: number]: Team.Members[] | undefined,
    };
    /** 战队动态 */
    moments: {
        [teamId: number]: ListReq & ListWrap<Team.Moments> | undefined,
    };
    requesting: boolean;
    teamInviteCard: {
        [teamId: number]: string | undefined,
    };
    teamReferrer: {
        [key: string]: string,
    };
}

const m: Module<TeamState, RootState> = {
    namespaced: true,
    state: {
        members: [],
        moments: [],
        requesting: false,
        teamInviteCard: {},
        teamReferrer: {},
    },
    mutations: {
        setMembers(state, { teamId, members }: { teamId: number, members: Team.Members[] }) {
            Vue.set(state.members, teamId, members);
        },
        setMoments(state, { teamId, moments }: { teamId: number, moments: Team.Moments[] }) {
            state.moments[teamId]!.list = state.moments[teamId]!.list.concat(moments);
        },
        setRequesting(state, requesting: boolean) {
            state.requesting = requesting;
        },
        setMomentLoaded(state, { teamId, loaded }: { teamId: number, loaded: boolean }) {
            state.moments[teamId]!.loaded = loaded;
        },
        setMomentRequesting(state, { teamId, requesting }: { teamId: number, requesting: boolean }) {
            state.moments[teamId]!.requesting = requesting;
        },
        setTeamInviteCardMap(state, { teamId, card }: { teamId: number, card: string }) {
            Vue.set(state.teamInviteCard, teamId, card);
        },
        setTeamReferrer(state, { key, data }: { key: string, data: string }) {
            Vue.set(state.teamInviteCard, key, data);
        },
    },
    actions: {
        /**
         * 获取战队成员列表
         *
         * @param {number} teamId
         *
         * @return {Object} rdata
         *  - status 0表示成功，-1表示失败
         *  - message 失败信息
         */
        async getTeamMembers(context, { teamId, forceUpdate }: { teamId: number, forceUpdate?: boolean }) {
            const response = await Vue.axios.get('/api/v1/activity/team_members', {
                params: {
                    id: teamId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                context.commit('setMembers', {
                    teamId,
                    members: rdata.data,
                });
            }
            return response.data;
        },
         /**
          * 获取战队成员列表
          * @param teamId
          * @param count
          * @param lastId
          * @param forceUpdate
          */
         async getTeamMoments({ state, commit }, { teamId, count, lastId, forceUpdate = false }: { teamId: number, count: number, lastId: number | null, forceUpdate: boolean }) {
            if (isUndef(state.moments[teamId])) {
                Vue.set(state.moments, String(teamId), {
                    list: [],
                    loaded: false,
                    requesting: false,
                });
            }
            const tempData = state.moments[teamId];
            if (tempData!.requesting || (tempData && tempData.loaded && !forceUpdate)) {
                return;
            }
            if (forceUpdate) {
                tempData!.loaded = false;
                tempData!.list = [];
                lastId = null;
            } else if (tempData) {
                lastId = tempData.list[tempData.list.length - 1].id;
            }
            commit('setMomentRequesting', {
                teamId,
                requesting: true,
            });
            const response = await Vue.axios.get('/api/v1/activity/team_moments', {
                params: {
                    id: teamId,
                    count,
                    last_id: lastId,
                },
            });
            const rdata = response.data;
            commit('setMomentRequesting', {
                teamId,
                requesting: false,
            });
            if (rdata.status === 0) {
                commit('setMoments', {
                    teamId,
                    moments: rdata.data,
                });
                if (rdata.data.length < count) {
                    commit('setMomentLoaded', {
                        teamId,
                        loaded: true,
                    });
                }
            }
            return rdata;
        },
        /**
         * 获得战队邀请卡
         *
         * @param {string} activityId
         * @param {number} teamId 战队id
         */
        async getTeamInviteCard({ state, commit }, { activityId, teamId, forceUpdate }: { activityId: string, teamId: number, forceUpdate?: boolean }) {
            const temp = state.teamInviteCard[teamId];
            if (temp && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/activity/get_team_invite_card_sync', {
                params: {
                    activity_id: activityId,
                    team_id: teamId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setTeamInviteCardMap', {
                    // activityId,
                    teamId,
                    card: rdata.data,
                });
            }
            return rdata;
        },
        /**
         * 记录战队详情页面的访问情况
         *
         * @param {string} referrer
         * @param {number} teamId 战队id
         */
        async recordTeam({ state, commit }, { referrer, teamId, forceUpdate}: { referrer: string, teamId: number, forceUpdate?: boolean }) {
            const key = referrer + '_' + teamId;
            const temp = state.teamReferrer[key];
            if (temp && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.post('/api/v1/activity/team_visit', {
                referrer,
                team_id: teamId,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setTeamReferrer', {
                    key,
                    data: '',
                });
            }
            return rdata;
        },
    },
};

export default m;
