import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import TeamApply from '@/models/teamApply';
import Toast from '@/components/toast/toast';
import MonitorApply from '@/models/monitorApply';

declare global {
    interface MonitorApplyStatus {
        applied: boolean;
        contract_signed: boolean;
        extra_info_filled: boolean;
        ever_became_monitor: boolean;
        // 最新邀请人id
        referrer: number;
        referrer_nickname: string;
        referrer_avatar: string;
        status: 'pending' | 'accepted' | 'rejected';
    }
}

export interface CreateTeamState {
    /** 队长申请数据 */
    monitorApply: MonitorApply | null;
    /** 队长申请状态 */
    monitorApplyStatus: MonitorApplyStatus | null;
    /** 如果用户已经创建过战队此字段为创建信息，否则返回null */
    teamApply: TeamApply | null;
}

const m: Module<CreateTeamState, RootState> = {
    namespaced: true,
    state: {
        monitorApply: null,
        monitorApplyStatus: null,
        teamApply: new TeamApply({}),
    },
    mutations: {
        /**
         * 记录队长申请状态
         */
        setMonitorApplyStatus(state, status: MonitorApplyStatus) {
            state.monitorApplyStatus = status;
        },
        /**
         * 记录申请
         * @param {} apply 申请数据
         */
        setTeamApply(state, apply) {
            state.teamApply = apply;
        },
    },
    actions: {
        /**
         * 检查队长申请状态
         */
        async checkMonitorApply({ state, commit }, { activityId }) {
            const response = await Vue.axios.get('/api/v1/monitor_apply/check', {
                params: {
                    activity_id: activityId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setMonitorApplyStatus', rdata.data);
            }
            return rdata;
        },
        /**
         * 同意协议
         */
        async acceptAgreement({ state, commit }, { activityId, agreed }) {
            const response = await Vue.axios.post('/api/v1/monitor_apply/create', {
                activity_id: activityId,
                contract_agreed: agreed,
            });
            return response.data;
        },
        /**
         * 提交队长额外信息
         */
        async submitMonitorInfo({ state, commit }, { activityId, anticipatePeople, referrer, referrerMobile, q1, q2, q3 }) {
            const response = await Vue.axios.post('/api/v1/monitor_apply/create', {
                activity_id: activityId,
                predicted_member_count: anticipatePeople,
                referrer,
                referrer_mobile: referrerMobile,
                how_to_know_hd: q1,
                how_much_know_hd: q2,
                recruit_method: q3,
            });
            return response.data;
        },
        /**
         * 获得战队申请信息
         * @param {} forceUpdate
         */
        async getTeamApply({ state, commit }, { activityId, forceUpdate = false }: { activityId: string, forceUpdate: boolean }) {
            if (state.teamApply && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/monitor_apply/team_apply', {
                params: {
                    id: activityId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0 && rdata.data) {
                commit('setTeamApply', new TeamApply(rdata.data));
            } else if (rdata.status === 0) {
                commit('setTeamApply', new TeamApply({}));
            } else {
                Toast(rdata.message);
            }
            return rdata;
        },
        /**
         * 申请创建战队
         * @param {} data 数据
         */
        async applyTeam(context, apply: TeamApply) {
            const data = apply.getSnake();
            // @ts-ignore
            if (data.city_id && data.city_id === -1) {
                // @ts-ignore
                data.city_id = null;
            }
            const response = await Vue.axios.post('/api/v1/monitor/create_team', data);
            const rdata = response.data;
            return rdata;
        },
    },
};
export default m;
