import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import { Blog, Comment, Reply, BlogType } from '@/models/blog';

export interface ComminityState {

    // 保存动态
    blogsMap: {
        [ id: number]: Blog,
    };

    blogIds: {
        hot: {
            list: number[],
            requesting: boolean,
            loaded: boolean,
        },
        all: {
            list: number[],
            requesting: boolean,
            loaded: boolean,
        },
        my: {
            list: number[],
            requesting: boolean,
            loaded: boolean,
        },
    };

    blogDetail: {
        [ id: number]: Blog,
    };

    commentMap: {
        [ id: number]: Comment,
    };

    commentIds: {
        [ id: number]: {
            list: number[], // id
            loaded: boolean,
        },
        requesting: boolean,
    };

    replyMap: {
        [ id: number]: Reply,
    };

    replyIds: {
        [ id: number]: {
            list: number[], // id
            loaded: boolean,
        },
        requesting: boolean,
    };

    isShowShare: boolean;

    nav: BlogType;

    offlineList: Community.OfflineItem[];
    bannerUrl: Community.BannerUrl[];
}

const m: Module<ComminityState, RootState> = {
    namespaced: true,

    state: {

        /** 动态map */
        blogsMap: {},

        /** 动态列表 */
        blogIds: {
            hot: {
                list: [],
                requesting: false,
                loaded: false,
            },
            all: {
                list: [],
                requesting: false,
                loaded: false,
            },
            my: {
                list: [],
                requesting: false,
                loaded: false,
            },
        },

        /**  动态详情 */
        blogDetail: {},

        /** 评论map */
        commentMap: {},

        /** 评论列表 */
        commentIds: {
            requesting: false,
        },

        /** 评论map */
        replyMap: {},

        /** 回复列表 */
        replyIds: {
            requesting: false,
        },

        /** 显示分享弹窗 */
        isShowShare: false,

        /** 导航栏 */
        nav: 'all',

        /** 线下活动列表 */
        offlineList: [],
        bannerUrl: [],
    },

    mutations: {

        /** 设置动态列表 */
        concatBlogs(state, { type, data }: { type: BlogType, data: Blog[]}) {
            const blogIds: number[] = [];
            data.forEach((blog) => {
                blogIds.push(blog.id);
                Vue.set(state.blogsMap, blog.id, blog);
            });
            state.blogIds[type].list = state.blogIds[type].list.concat(blogIds);
        },

        /**  设置请求 */
        setBlogsRequest(state, { type, data }: { type: BlogType, data: boolean}) {
            state.blogIds[type].requesting = data;
        },

        /** 设置加载 */
        setBlogsLoad(state, { type, data }: { type: BlogType, data: boolean}) {
            state.blogIds[type].loaded = data;
        },

        /** 删除一条动态 */
        removeBlog(state, data: Blog) {
            const blogTypes: BlogType[] = [ 'all', 'my', 'hot'];
            blogTypes.forEach((blogType) => {
                const blogIdList = state.blogIds[blogType].list;
                if (blogIdList.includes(data.id)) {
                    blogIdList.splice(blogIdList.indexOf(data.id), 1);
                }
            });
        },

        /** 设置动态详情 */
        setBlogDetail(state, { id, data }) {
            Vue.set(state.blogDetail, id, data);
        },


         /** 设置评论列表 */
        setComments(state, { blogId, list }) {
            if (!state.commentIds[blogId]) {
                Vue.set(state.commentIds, blogId, {
                    list: [],
                    loaded: false,
                });
            }
            const commentIds: number[] = [];
            list.forEach((comment: Comment) => {
                commentIds.push(comment.id);
                Vue.set(state.commentMap, comment.id, comment);
                if (comment.replies.length > 0) { // 处理评论携带的最新两条回复
                    const replyIds: number[] = [];
                    comment.replies.forEach((reply) => {
                        replyIds.push(reply.id);
                        Vue.set(state.replyMap, reply.id, reply);
                    });
                    Vue.set(state.replyIds, comment.id, {
                        list: [],
                        loaded: false,
                    });
                    // 因为重新请求了数据，清空一下回复列表
                    state.replyIds[comment.id].list = [];
                    state.replyIds[comment.id].list = state.replyIds[comment.id].list.concat(replyIds);
                }
            });
            state.commentIds[blogId].list = state.commentIds[blogId].list.concat(commentIds);
        },

        /**  设置评论请求 */
        setCommentsRequest(state, data) {
            state.commentIds.requesting = data;
        },

        /** 设置评论加载 */
        setCommentsLoad(state, { blogId, loaded }) {
            state.commentIds[blogId].loaded = loaded;
        },


         /** 设置回复列表 */
        setReplys(state, { commentId, list }) {
            const replyIds: number[] = [];
            if (!state.replyIds[commentId]) {
                Vue.set(state.replyIds, commentId, {
                    list: [],
                    loaded: false,
                });
            }
            list.forEach((reply: Reply) => {
                replyIds.push(reply.id);
                Vue.set(state.replyMap, reply.id, reply);
            });
            state.replyIds[commentId].list = state.replyIds[commentId].list.concat(replyIds);
        },

        /**  设置回复请求 */
        setReplysRequest(state, data) {
            state.replyIds.requesting = data;
        },

        /** 设置回复加载 */
        setReplysLoad(state,  { commentId, loaded }) {
            state.replyIds[commentId].loaded = loaded;
        },

        /** 是否显示分享弹窗 */
        setShowShare(state, data) {
            state.isShowShare = data;
        },

        /** 标记动态赞 */
        setBlogLike(state, data: Blog) {
            const count = data.like_count + 1;
            if (state.blogsMap[data.id]) {
                Vue.set(state.blogsMap[data.id], 'like_count', count);
                Vue.set(state.blogsMap[data.id], 'already_like', true);
            }
            if (state.blogDetail[data.id]) {
                Vue.set(state.blogDetail[data.id], 'like_count', count);
                Vue.set(state.blogDetail[data.id], 'already_like', true);
            }
        },

        /** 取消动态赞 */
        setBlogDislike(state, data: Blog) {
            const count = data.like_count - 1;
            if (state.blogsMap[data.id]) {
                Vue.set(state.blogsMap[data.id], 'like_count', count);
                Vue.set(state.blogsMap[data.id], 'already_like', false);
            }
            if (state.blogDetail[data.id]) {
                Vue.set(state.blogDetail[data.id], 'like_count', count);
                Vue.set(state.blogDetail[data.id], 'already_like', false);
            }
        },

        /** 添加评论 */
        addBlogComment(state, { id, content }: { id: number, content: string}) {
            if (state.blogDetail[id]) {
                const count = state.blogDetail[id].comment_count + 1;
                Vue.set(state.blogDetail[id], 'comment_count', count);
            }
            if (state.blogsMap[id]) {
                const count = state.blogsMap[id].comment_count + 1;
                Vue.set(state.blogsMap[id], 'comment_count', count);
            }
        },

        /** 删除评论 */
        removeBlogComment(state, { blogId, comment }: { blogId: number, comment: Comment}) {
            const count = state.blogDetail[blogId].comment_count - 1;
            Vue.set(state.blogDetail[blogId], 'comment_count', count);
            // 动态列表有这条数据
            if (state.blogsMap[blogId]) {
                Vue.set(state.blogsMap[blogId], 'comment_count', count);
            }
            state.commentIds[blogId].list.splice(state.commentIds[blogId].list.indexOf(comment.id), 1);
        },

        /** 删除回复 */
        removeReply(state, { commentId, replyId }) {
            state.replyIds[commentId].list.splice(state.replyIds[commentId].list.indexOf(replyId), 1);
            state.commentMap[commentId]!.reply_count! --;
        },

        /** 设置导航栏 */
        setNav(state, nav: BlogType) {
            state.nav = nav;
        },

        /** 设置线下活动列表 */
        setOfflineList(state, list: Community.OfflineItem[]) {
            state.offlineList = list;
        },

        /** 设置顶部banner链接 */
        setBannerUrl(state, url) {
            state.bannerUrl = url;
        },
    },

    actions: {
        /**
         * 获取动态列表
         * @param count 数量
         * @param last_id 最后一条id
         * @param mine 类型
         */
        async getBlogs({ state, commit }, { count, lastId, type, forceUpdate = false }: {
            count: number, lastId: number | null, type: BlogType, forceUpdate: boolean }) {
            if (forceUpdate) {
                state.blogIds[type].requesting = false;
                state.blogIds[type].loaded = false;
            }

            if (!lastId && !forceUpdate && state.blogIds[type].list.length > 0 ) {
                return;
            }

            if (state.blogIds[type].requesting || state.blogIds[type].loaded) {
                return;
            }
            commit('setBlogsRequest', { type, data: true });
            const response = await Vue.axios.get('/api/v1/community/moment_list', {
                params: {
                    count,
                    last_id: lastId,
                    mine: type,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                if (forceUpdate) {
                    state.blogIds[type].list = [];
                }
                commit('concatBlogs', { type, data: rdata.data });
                if (rdata.data.length < count) {
                    commit('setBlogsLoad', { type, data: true });
                }
            }
            commit('setBlogsRequest',  { type, data: false });
            return rdata;
        },

        /**
         * 发表动态
         * @param content 文字儿
         * @param images 图片儿
         * @param position 定位
         */
        async setBlog(context, { content, images, position, sign_in_id}) {
            const response = await Vue.axios.post('/api/v1/community/create_moment', {
                content,
                images,
                position,
                sign_in_id,
            });
            const rdata = response.data;
            return rdata;
        },

        /**
         * 获取动态详情
         * @param id 动态id
         */
        async getBlogDetail({ state, commit }, id: number) {
            if (state.blogDetail[id]) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/community/moment_detail', {
                params: {
                    id,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setBlogDetail', { id, data: rdata.data });
            }
            return rdata;
        },

        /**
         * 获取评论列表
         * @param id 动态id
         */
        async getBlogComments({ state, commit }, { blogId, lastId, count, forceUpdate = false }) {
            const data = state.commentIds[blogId] ? state.commentIds[blogId] : { list: [], loaded: false };
            if (state.commentIds.requesting || (data.loaded && !forceUpdate)) {
                return;
            }
            if (forceUpdate && !lastId) {
                data.list = [];
                data.loaded = false;
            }
            commit('setCommentsRequest', true);
            const response = await Vue.axios.get('/api/v1/community/comment_list', {
                params: {
                    id: blogId,
                    count,
                    last_id: lastId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setComments', {
                    blogId,
                    list: rdata.data,
                });
                if (rdata.data.length < count) {
                    commit('setCommentsLoad', {
                        blogId,
                        loaded: true,
                    });
                }
            }
            commit('setCommentsRequest', false);
            return rdata;
        },

        /**
         * 获取回复列表
         * @param id 评论id
         */
        async getBlogReply({ state, commit }, { commentId, lastId, count, forceUpdate = false }) {
            const data = state.replyIds[commentId] ? state.replyIds[commentId] : { list: [], loaded: false };
            if (state.replyIds.requesting || (data.loaded && !forceUpdate)) {
                return;
            }
            if (forceUpdate && !lastId) {
                data.list = [];
                data.loaded = false;
            }
            commit('setReplysRequest', true);
            const response = await Vue.axios.get('/api/v1/community/reply_list', {
                params: {
                    id: commentId ,
                    count,
                    last_id: lastId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setReplys', {
                    commentId,
                    list: rdata.data,
                });
                if (rdata.data.length < count) {
                    commit('setReplysLoad', {
                        commentId,
                        loaded: true,
                    });
                }
            }
            commit('setReplysRequest', false);
            return rdata;
        },

        /**
         * 发表评论
         * @param content 文字儿
         * @param id 动态id
         */
        async setBlogComment(context, { id, content }) {
            const response = await Vue.axios.post('/api/v1/community/create_comment', {
                content,
                id,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                context.commit('addBlogComment', { id, content });
            }
            return rdata;
        },

        /**
         * 发表回复
         * @param content 文字儿
         * @param id (评论的id)
         * @param reply_to 回复某个用户的用户id
         * @param reply_id 回复的id
         */
        async setBlogReply(context, { content, id, reply_to, reply_id }) {
            const response = await Vue.axios.post('/api/v1/community/create_reply', {
                content,
                id,
                reply_to,
                reply_id,
            });
            const rdata = response.data;
            return rdata;
        },

        /**
         * 删除动态
         * @param id
         */
        async deleteBlog(context, blog: Blog) {
            const response = await Vue.axios.post('/api/v1/community/delete_moment', {
                id: blog.id,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                context.commit('removeBlog', blog);
            }
            return rdata;
        },

        /**
         * 删除评论
         * @param id 动态id
         * @param comment: 评论内容
         */
        async deleteBlogComment(context, { blogId, comment }: { blogId: number, comment: Comment}) {
            const response = await Vue.axios.post('/api/v1/community/delete_comment', {
                id: comment.id,
            });
            const rdata = response.data;
            if (rdata.status === 0 ) {
                context.commit('removeBlogComment', { blogId, comment });
            }
            return rdata;
        },

        /**
         * 删除回复
         * @param id
         */
        async deleteBlogReply(context, { commentId, replyId }) {
            const response = await Vue.axios.post('/api/v1/community/delete_reply', {
                id: replyId,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                context.commit('removeReply', { commentId, replyId });
            }
            return rdata;
        },

        /**
         * 点赞
         * @param id
         */
        async setBlogLike(context, blog: Blog) {
            const response = await Vue.axios.post('/api/v1/community/like', {
                id: blog.id,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                context.commit('setBlogLike', blog);
            }
            return rdata;
        },

        /**
         * 取消点赞
         * @param id
         */
        async setBlogDislike(context, blog: Blog) {
            const response = await Vue.axios.post('/api/v1/community/cancel_like', {
                id: blog.id,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                context.commit('setBlogDislike', blog);
            }
            return rdata;
        },

        /**
         * 举报
         * @param id
         */
        async setBlogReport(context, id: number) {
            const response = await Vue.axios.post('/api/v1/community/report', {
                id,
            });
            const rdata = response.data;
            return rdata;
        },

        /**
         * 获取线下活动列表
         */
        async getOfflineList({ commit }) {
            const response = await Vue.axios.get('/api/v1/community/under_line_activity');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setOfflineList', rdata.data);
            }
            return rdata;
        },

        /** 获取banner的url */
        async getBannerUrl({ commit }) {
            const response = await Vue.axios.get('/api/v1/community/community_banner');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setBannerUrl', rdata.data);
            }
            return rdata;
        },
    },
};

export default m;
