import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';

export interface ShopState {
    /** 礼品订单列表 */
    giftOrders: {
        [activityId: string]: Shop.GiftOrder[];
    };
    order: {
        [orderId: string]: Shop.GiftOrder;
    };
    /** 已发货礼品订单数量 */
    // sentOrderNumber: number;
}

const m: Module<ShopState, RootState> = {
    namespaced: true,

    state: {
        /** 礼品订单列表 */
        giftOrders: {},
        /** 订单详情 */
        order: {},
    },

    mutations: {
        /** 设置礼品订单列表和已发货礼品订单数量 */
        setGiftOrders(state, { activityId, orders }) {
            Vue.set(state.giftOrders, activityId, orders);
        },

        /** 设置订单详情 */
        setGiftOrderDetail(state, { serialNumber, order }) {
            Vue.set(state.order, serialNumber, order);
        },
    },

    actions: {
        /**
         *  获取礼品订单列表
         * @param activityId 活动id
         */
        async getGiftOrders({ commit }, { activityId, offset, count }) {
            const response = await Vue.axios.get('/api/v1/gift/orders', {
                params: {
                    activity_id: activityId,
                    offset,
                    count,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setGiftOrders', { activityId, orders: rdata.data });
            }
        },
        /**
         * 获取订单详情
         * @param serialNumber 订单号
         */
        async getGiftOrderDetail({ commit }, serialNumber) {
            const response = await Vue.axios.get('/api/v1/gift/order_detail', {
                params: {
                    order_id: serialNumber,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setGiftOrderDetail', { serialNumber, order: rdata.data });
            }
        },
    },
};

export default m;

