import Vue from 'vue';
import OriginToast from './toastView.vue';

declare global {
    interface ToastOptions {
        /** 消息内容 */
        message?: string;
        /** 时长 */
        duration?: number;
        /** icon样式 */
        iconClass?: string;
        className?: string;
        position?: string;
    }
}

const ToastConstructor = Vue.extend(OriginToast);
const toastPool: any[] = [];

const getAnInstance = () => {
    if (toastPool.length > 0) {
        const instance = toastPool[0];
        toastPool.splice(0, 1);
        return instance;
    }
    return new ToastConstructor({
        el: document.createElement('div'),
    });
};

const returnAnInstance = (instance: any) => {
    if (instance) {
        toastPool.push(instance);
    }
};

const removeDom = (event: TransitionEvent) => {
    if (!event || !event.target) {
        return;
    }
    // @ts-ignore
    if (event.target.parentNode) {
        // @ts-ignore
        event.target.parentNode.removeChild(event.target);
    }
};

ToastConstructor.prototype.close = function() {
    this.visible = false;
    this.$el.addEventListener('transitionend', removeDom);
    this.closed = true;
    returnAnInstance(this);
};

const Toast = (options: string | ToastOptions) => {
    if (!options) {
        return;
    }
    if (typeof options === 'string') {
        options = {
            message: options,
        };
    }
    const duration = options.duration || 3000;

    const instance = getAnInstance();
    instance.closed = false;
    clearTimeout(instance.timer);
    instance.message = typeof options === 'string' ? options : options.message;
    instance.position = options.position || 'middle';
    instance.className = options.className || '';
    instance.iconClass = options.iconClass || '';

    document.body.appendChild(instance.$el);
    Vue.nextTick(() => {
        instance.visible = true;
        instance.$el.removeEventListener('transitionend', removeDom);
        if (duration !== -1) {
            instance.timer = setTimeout(() => {
                if (instance.closed) {
                    return;
                }
                instance.close();
            }, duration);
        }
    });
    return instance;
};

export default Toast;
