/**
 * 是否是闰年
 */
export function isLeapYear(year: number) {
    if (year % 4 !== 0) {
        return false;
    }
    if (year % 400 === 0) {
        return true;
    }
    if (year % 100 === 0) {
        return false;
    }
    return true;
}

/**
 * 获得一个月有多少天
 *
 * @param {} year
 * @param {} month 1-12
 */
export function getMonthDay(year: number, month: number) {
    if (isLeapYear(year) && month === 2) {
        return 29;
    }
    return monthDay[month];
}

/**
 * 等待毫秒数
 */
export function wait(ms: number) {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve();
        }, ms);
    });
}

/**
 * 获得querystring
 *
 * @return {object} 返回qs
 */
export function getQS() {
    const posInSearch = location.search.indexOf('?');
    return querystring.parse(location.search.substr(posInSearch + 1));
}
/**
 * 是否是null或者undefined
 *
 * @param {} v 参数
 *
 * @return {boolean}
 */
export function isUndef(v: any): v is (null | undefined) {
    return v === null || v === undefined;
}

/**
 * 金额格式化，当存在小数时，将显示两位小数
 *
 * @param {} v 金额数据
 *
 * @return {string}
 */
export function currency(v: number | null | undefined) {
    if (isUndef(v)) {
        return v;
    }

    const result = String(((v as number) / 100).toFixed(2));
    const pos = result.indexOf('.00');
    if (pos !== -1) {
        return result.substr(0, pos);
    } else {
        return result;
    }
}

/**
 * 返回weekDay的中文
 *
 * @param {Date} date
 */
export function toChWeekDay(date: Date, format: string) {
    if (isUndef(date)) {
        return date;
    }
    const day = new Date(date.valueOf() - 8 * 3600000).getUTCDay();
    if (isUndef(chWeekDay[day])) {
        return day;
    }
    if (!isUndef(format)) {
        return format.replace(/d/g, chWeekDay[day]);
    } else {
        return chWeekDay[day];
    }
}

/**
 * 获得当前的时间
 *
 * @return {Date} 当天
 */
export function dateNow() {
    const utcDate = new Date(new Date().valueOf() + 8 * 3600000);
    const year = utcDate.getUTCFullYear();
    const month = utcDate.getUTCMonth() + 1;
    const date = utcDate.getUTCDate();
    const hour = utcDate.getUTCHours();
    const minute = utcDate.getUTCMinutes();
    const second = utcDate.getUTCSeconds();
    return dateParse(`${year}-${month}-${date} ${hour}:${minute}:${second}`);
}

/**
 * 日期格式化
 *
 * @param {Date|Number} v UTC+08:00
 * @param {String} format 格式内容
 *
 * @return {string} 经过format的日期数据
 */
export function dateFormat(v: number | Date, format?: string | null): any {
    const type = typeof v;
    if (isUndef(v) || (type !== 'number' && !(v instanceof Date))) {
        return v;
    }
    // 使用默认的format
    if (!format) {
        format = 'Y-m-d';
    }
    if (typeof v === 'number') {
        v = new Date(v - 8 * 3600000);
    } else {
        v = new Date(v.valueOf() - 8 * 3600000);
    }

    format = format!.replace('Y', String(v.getUTCFullYear()));
    format = format!.replace('m', String(_format(v.getUTCMonth() + 1)));
    format = format!.replace('d', String(_format(v.getUTCDate())));
    format = format!.replace('H', String(_format(v.getUTCHours())));
    format = format!.replace('i', String(_format(v.getUTCMinutes())));
    format = format!.replace('s', String(_format(v.getUTCSeconds())));

    return format;
}

/**
 * 移动日期
 *
 * @param {Date} v 传入进来的日期
 * @param {} day
 */
export function dateOffset(v: Date, day: number) {
    if (isUndef(v)) {
        return v;
    }
    return new Date(v.valueOf() + day * 86400000);
}

/**
 * 日期天数差距
 *
 * @param {} v 较小的日期
 * @param {} c 比较的日期
 *
 * @return {number} 差距天数，当差距小于1天的时候，将不进行处理
 */
export function dateDiff(v: Date, c: Date) {
    if (isUndef(v)) {
        return v;
    }
    return Math.floor((c.valueOf() - v.valueOf()) / 86400000);
}

/**
 * 日期文本解析
 *
 * @param {String} v 要求必须是 1. Y-m-d H:i:s 2. Y-m-d
 *
 * @return {Date} 解析后的日期 UTC+08:00
 */
export function dateParse(v: string): Date; // 重载
export function dateParse(v: '' | null | undefined): null | undefined;
export function dateParse(v: any): any {
    if (isUndef(v) || v === '') {
        return v;
    }
    const chunk = v.split(' ');
    const date = chunk[0];
    const time = chunk[1];
    const dateChunk = date.split('-');
    const d = new Date();
    d.setUTCFullYear(Number(dateChunk[0]));
    d.setUTCMonth(Number(dateChunk[1]) - 1);
    d.setUTCMonth(Number(dateChunk[1]) - 1);
    d.setUTCDate(Number(dateChunk[2]));
    let hour = 0;
    let minute = 0;
    let second = 0;
    if (!isUndef(time)) {
        const timeChunk = time.split(':');
        hour = Number(timeChunk[0]);
        minute = Number(timeChunk[1]);
        second = Number(timeChunk[2]);
    }
    d.setUTCHours(hour);
    d.setUTCMinutes(minute);
    d.setUTCSeconds(second);
    d.setUTCMilliseconds(0);
    return new Date(d.valueOf() + 8 * 3600000);
}

/**
 * 获得当天的Date，要求北京时区
 *
 * @return {Date} 当天日期，UTC时间+8小时
 */
export function dateToday() {
    const utcDate = new Date(new Date().valueOf() + 8 * 3600000);
    return dateParse(`${utcDate.getUTCFullYear()}-${utcDate.getUTCMonth() + 1}-${utcDate.getUTCDate()} 00:00:00`);
}

/**
 * 某一天之后的第一个day
 *
 *  - 当是同一个day的时候会获取当天(默认行为)，当inclusive = false是，将会寻找下一周的同一day
 *
 * @param {} date 某一天的日期
 * @param {} day 周几
 * @param {} inclusive = true 是否包含当天
 */
export function dateNext(date: Date, day: number, inclusive = true) {
    let offset = (day - date.getDay() + 7) % 7;
    if (!inclusive && offset === 0) {
        offset = 7;
    }
    // 得到今天后的周几那天的日期
    return dateOffset(date, offset);
}

export function getYear(date: Date) {
    return date.getUTCFullYear();
}
export function getMonth(date: Date) {
    return date.getUTCMonth();
}
export function getDay(date: Date) {
    return date.getUTCDay();
}
/**
 * 转换成[x天]xx:xx:xx格式
 * @param minute 允许小数
 */
export function minuteFormat(minute: number) {
    const day = Math.floor(minute / 1440);
    const dayStr = day > 0 ? day + '天' : '';
    return dayStr + `${_format(Math.floor(minute / 60) % 24)}:${_format(Math.floor(minute % 60))}:${_format(Math.round((minute % 1) * 60))}`;
}
export function paceFormat(pace: number) {
    return `${Math.floor(pace)}'${_format(Math.floor((pace % 1) * 60))}"`;
}
/**
 * 获取配速
 * @param distance 距离，公里
 * @param time 时长，分钟
 */
export function getPace(distance: number, time: number) {
    return time / distance;
}

export function floorOrFixed(val: number, precision: number = 2) {
    const str = val + '';
    return str.indexOf('.') >= 0 ? val.toFixed(2) : str;
}

/**
 * 对于数字进行format操作
 */
export function _format(n: number) {
    if (n >= 0 && n < 10) {
        return '0' + n;
    } else {
        return `${n}`;
    }
}

// 设置分享话术
export function registerShare(option: ShareOption) {
    if (option.link && typeof option.link !== 'string') {
        const qs = {
            // 将query对象进行解构
            ...getQS(),
            ...(option.link.query ? option.link.query : {}),
        };
        option.link = `${window.pageUrl}${window.appPageRoot}?${querystring.stringify(qs)}#${option.link.path}`;
    }
    if (!option.imgUrl) {
        const basicInfo = store.state.activity.basicInfo[store.getters['activity/activityId']];
        if (basicInfo) {
            option.imgUrl = basicInfo.share_logo;
        }
    }

    if (typeof wx !== 'undefined') {
        store.commit('addJSSDKReadyCallback', () => {
            // @ts-ignore
            wx.onMenuShareTimeline(option); // 分享到朋友圈
            // @ts-ignore
            wx.onMenuShareAppMessage(option); // 分享给朋友
            // @ts-ignore
            wx.onMenuShareQQ(option); // 分享给qq
            // @ts-ignore
            wx.onMenuShareWeibo(option); // 分享给腾讯微博
            // @ts-ignore
            wx.onMenuShareQZone(option); // 分享到QQ空间
        });
    }
}

/**
 * 去除字符串中的空格
 * @param  String str 需要去除空格的字符串
 */
export function removeAllSpace(str: string | null) {
    return str ? str.replace(/\s+/g, '') : '';
}

/**
 * 加载图片
 */
export function loadImage(src: string, anonymous?: boolean) {
    return new Promise<HTMLImageElement>((resolve, reject) => {
        const image = new Image();
        if (anonymous) {
            image.setAttribute('crossOrigin', 'anonymous');
        }
        image.onload = () => {
            resolve(image);
        };
        image.onerror = (e) => {
            reject(e);
        };
        image.src = src;
    });
}

export const is = {
    weixin() {
        return /MicroMessenger/.test(navigator.userAgent);
    },
    tourye() {
        return /Tourye/.test(navigator.userAgent);
    },
    bairipao() {
        return /Bairipao/.test(navigator.userAgent);
    },
    /** 是否是iPhone设备 */
    iPhone() {
        return /iPhone/.test(navigator.userAgent);
    },
    /** 设备是否是iPhoneX/XS/XSMax/XR */
    iPhoneXSeries() {
        return is.iPhoneXOrXS() || is.iPhoneXSMax() || is.iPhoneXSMax() || is.iPhoneXR();
    },
    /** iPhoneXSMax */
    iPhoneXOrXS() {
        return is.iPhone() && window.devicePixelRatio === 3 && window.screen.width === 375 && window.screen.height === 812;
    },
    /** iPhoneXSMax */
    iPhoneXSMax() {
        return is.iPhone() && window.devicePixelRatio === 3 && window.screen.width === 414 && window.screen.height === 896;
    },
    /** iPhoneR */
    iPhoneXR() {
        return is.iPhone() && window.devicePixelRatio === 2 && window.screen.width === 414 && window.screen.height === 896;
    },
};

/**
 * 生成map
 * @param {} arr
 * @param {} key
 */
export function makeMap(arr: any[], key: string, setter?: (v: any) => any) {
    const map: {
        [propName: string]: any,
    } = {};
    for (let i = 0, len = arr.length; i < len; i++) {
        map[arr[i][key]] = setter ? setter(arr[i]) : arr[i];
    }
    return map;
}

