/**
 * 获得当前的时间
 *
 * @return {Date} 当天
 */
export function serverDateNow() {
    if (!store.state.util.timestamp) {
        return dateNow();
    }
    const timestamp: number = store.state.util.timestamp * 1000;
    const utcDate = new Date(timestamp + 8 * 3600000);
    const year = utcDate.getUTCFullYear();
    const month = utcDate.getUTCMonth() + 1;
    const time = `${utcDate.getUTCDate()} ${utcDate.getUTCHours()}:${utcDate.getUTCMinutes()}:${utcDate.getUTCSeconds()}`;
    return dateParse(`${year}-${month}-${time}`);
}

export function camelCase(key: string) {
    return key.replace(/[-_][a-z0-9]/g, (c) => c.substr(1).toUpperCase());
}

/**
 * 处理placeholder
 *
 * @param {} val
 * @param {} text
 */
export function placeholder(val: any, text: any, extra: any) {
    if (isUndef(val) || val === extra) {
        return text;
    }
    return val;
}

