/** 获取定位 */
export function getWxLocation() {
    if (!is.weixin()) {
        // 返回void则表明不处理
        return Promise.resolve();
    }
    return new Promise<{
        status: 'success' | 'error',
        result?: string,
        message?: string,
        [key: string]: any,
    }>((resolve, reject) => {
        store.commit('addJSSDKReadyCallback', () => {
            wx.getLocation({
                type: 'gcj02', // 返回可以用于wx.openLocation的经纬度
                // @ts-ignore
                success: (res: {
                    errMsg: string,
                    latitude: number;
                    longitude: number;
                    speed: any;
                    accuracy: any;
                }) => {
                    if (res.errMsg === 'getLocation:ok') {
                        resolve({
                            status: 'success',
                            res: {
                                latitude: res.latitude,
                                longitude: res.longitude,
                                speed: res.speed,
                                accuracy: res.accuracy,
                            },
                        });
                    } else {
                        resolve({
                            status: 'error',
                            result: res.errMsg,
                            message: res.errMsg,
                        });
                    }
                },
                // @ts-ignore
                fail: (res: WeixinJSSDKCallbackParams) => {
                    resolve({
                        status: 'error',
                        result: res.errMsg,
                        message: res.errMsg,
                    });
                },
            });
        });
    });
}

/**
 * 是否是某种类型
 */
export function isType<T>(name: string) {
    return (val: any): val is T => {
        return Object.prototype.toString.call(val) === `[object ${name}]`;
    };
}

export const isArray: <T = any>(val: any) => val is T[] = Array.isArray || isType(name);
export const isString: (val: any) => val is string = isType<string>('String');
export const isNumber: (val: any) => val is number = isType<number>('Number');
export const isFunction = isType('Function');

/**
 * 替换字符串中的某个字段
 * @param {String|Array} str      包含待替换字段的字符串
 * @param {Object} nickname 当前用户的nickname
 */
export function replace(str: string | string[], data: { [key: string]: string }): string | string[] {
    if (!str) {
        return str;
    }
    const result: string[] = [];
    if (!isArray(str)) {
        result.push(str);
    }
    const pending = [];
    for (let i = 0; i < result.length; i++) {
        if (!result[i]) {
            continue;
        }
        const pos = result[i].indexOf('${');
        if (pos >= 0) {
            pending.push(i);
        }
    }

    const keys = Object.keys(data);
    let regex = null;
    for (const key of keys) {
        regex = new RegExp('\\${' + key + '}', 'g');

        for (const index of pending) {
            result[index] = result[index].replace(regex, data[key]);
        }
    }
    return isArray(str) ? result : result[0];
}

export function repeat(str: string, times: number) {
    let result = '';
    for (let i = 0; i < times; i++) {
        result += '*';
    }
    return result;
}

export function securityText(str: string, rule: string) {
    const parts = rule.split('|');
    for (let i = 0, len = parts.length; i < len; i++) {
        const part = parts[i];
        const [ key, value ] = part.split(':');
        const reverse = key[0] === '!';
        const pureKey = key[0] === '!' ? key.substr(1) : key;
        const strlen = str.length;

        switch (pureKey) {
            // 从head开始计算
            case 'head':
                const hpos = Number(value);
                str = !reverse ? repeat('*', hpos) + str.substring(hpos) : str.substring(0, hpos) + repeat('*', str.length - hpos);
                break;
            // 从tail开始计算
            case 'tail':
                const tpos = Number(value);
                str = !reverse ? str.substring(0, strlen - tpos) + repeat('*', tpos) : repeat('*', strlen - tpos) + str.substring(strlen - tpos);
                break;
        }
    }
    return str;
}

export function isPopupDisplay(route: Route, tag?: string) {
    if (!route.meta) {
        return;
    }
    const popup = route.meta.popup;
    return (popup === true || (popup && ( popup === tag || popup.indexOf(tag) >= 0)));
}

/** 文字过长处理
 *
 * @param {string} str 需要处理的文字
 * @param {number} limit 文字长度限制
 */
export function overlongTextProcessing(str: string, limit: number) {
    if (str.length < limit) {
        return str;
    }
    return `${str.slice(0, limit)}...`;
}


const bodyEl = document.body;
let enabled = false;
let top = 0;
export function enableBodyScroll() {
    if (!enabled) {
        bodyEl.style.position = '';
        bodyEl.style.top = '';
        window.scrollTo(0, top); // 回到原先的top
        enabled = true;
    }
}

export function disableBodyScroll() {
    top = window.pageYOffset;
    bodyEl.style.position = 'fixed';
    bodyEl.style.top = -top + 'px';
    enabled = false;
}

