import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';

export interface InviteState {
    invite: ListWrap<Invite.InviteItem> & ListReq;
    stat: {
        /** 包括sign_up_count */
        affect_count: number,
        sign_up_count: number,
    } | null;
    gift: Invite.Gift[] | null;
    /** top10 */
    top10: string[] | null;
}

const m: Module<InviteState, RootState> = {
    namespaced: true,
    state: {
        gift: null,
        invite: {
            list: [],
            loaded: false,
            requesting: false,
        },
        stat: null,
        top10: null,
    },
    mutations: {
        setGift(state, gift) {
            state.gift = gift;
        },
        concatList(state, list) {
            state.invite.list.push(...list);
        },
        setListLoaded(state, loaded) {
            state.invite.loaded = loaded;
        },
        setListReq(state, req) {
            state.invite.requesting = req;
        },
        setStat(state, stat) {
            state.stat = stat;
        },
        setTop10(state, top10) {
            state.top10 = top10;
        },
    },
    actions: {
        /**
         * 获得礼品
         */
        async getGift({ state, commit }, { activityId }) {
            const response = await Vue.axios.get('/api/v1/invite/gift_list', {
                params: {
                    id: activityId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setGift', rdata.data);
            }
            return rdata;
        },
        /**
         * 获得邀请列表
         */
        async getList({ state, commit }, { activityId, lastId, count }) {
            if (state.invite.requesting || state.invite.loaded) {
                return;
            }

            commit('setListReq', true);
            const response = await Vue.axios.get('/api/v1/invite/list', {
                params: {
                    id: activityId,
                    last_id: lastId,
                    count,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                const list = rdata.data;
                commit('concatList', list);
                if (list.length < count) {
                    commit('setListLoaded', true);
                }
            }
            commit('setListReq', false);
            return rdata;
        },
        /**
         * 获取邀请统计
         */
        async getStat({ state, commit }, { activityId }) {
            const response = await Vue.axios.get('/api/v1/invite/stats', {
                params: {
                    id: activityId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setStat', rdata.data);
            }
            return rdata;
        },
        async getTop10({ state, commit }, { activityId }) {
            const response = await Vue.axios.get('/api/v1/invite/top_10', {
                params: {
                    id: activityId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setTop10', rdata.data);
            }
            return rdata;
        },
        /**
         * 获得邀请卡
         */
        async getInviteCard({ state, commit }, { activityId, teamId, style }) {
            const response = await Vue.axios.get('/api/v1/invite/invite_card', {
                params: {
                    activity_id: activityId,
                    team_id: teamId,
                    style,
                },
            });
            const rdata = response.data;
            return rdata;
        },
        /**
         * 获得邀请卡
         */
        async getMemberCard({ state, commit }, { teamId }) {
            const response = await Vue.axios.get('/api/v1/invite/member_card', {
                params: {
                    team_id: teamId,
                },
            });
            const rdata = response.data;
            return rdata;
        },
    },
};

export default m;
