



































import { Component, Vue, Watch } from 'vue-property-decorator';
import store from '@/store/index';
import { set, get } from '@/lib/service/storage';
import {
    isUndef,
    serverDateNow,
    dateParse,
    dateOffset,
    dateNow,
    dateFormat,
} from '@/lib/utils';
import QrcodePopup from '@/components/popup/qrcodePopup.vue';
import DistrictPicker from '@/features/address/districtPicker.vue';
import {
    ROUTE_NAME,
    datePopup,
} from '@/lib/constant';


@Component({
    name: 'App',
    components: {
        QrcodePopup,
        DistrictPicker,
    },
})
export default class App extends Vue {
    public topVisiable: boolean = false;
    public scroll: boolean = true;

    /** 请求提示数量 */
    public get requesting() {
        return store.state.util.requesting;
    }
    /** 错误提示数量 */
    public get errorCount() {
        return store.state.error.errorCount;
    }
    /** 错误信息 */
    public get errorInfo() {
        return store.state.error.errorInfo || {
            type: 'icon',
            icon: '&#xe621;',
            text: '发生了一些错误',
        };
    }
    /** 提示是否可见 */
    public get indicatorVisible() {
        return this.requesting + this.errorCount > 0;
    }
    /** 地址是否可见 */
    public get district() {
        return store.state.util.district;
    }
    public get activity2Detail() {
        return store.state.activity.basicInfo[2];
    }
    /** 倒计时显示的弹窗图片内容 */
    public get countdownImage() {
        const key = dateFormat(dateNow());
        return datePopup[key];
    }

    public onDistrictUpdate(names: string, ids: string) {
        if (this.district.update) {
            this.district.update(names, ids);
        }
    }
    public onDistrictFinish() {
        if (this.district.finish) {
            this.district.finish();
        }
    }
    public scrollCallback() {
        if (!this.scroll) {
            return;
        }
        const scroll_top = window.pageYOffset || document.documentElement.scrollTop;
        if (scroll_top > window.innerHeight * 2.3) {
            this.topVisiable = true;
        } else {
            this.topVisiable = false;
        }
        return true;
    }
    /** 点击回到顶部 */
    public onClickGoTop() {
        window.scrollTo(0, 0);
    }

    public mounted() {
        store.commit('addScrollCallback', this.scrollCallback.bind(this));
    }
    public beforeDestroy() {
        this.scroll = false;
    }
}
