import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import {
    chooseWXPay,
} from '@/lib/script/pay';

const m: Module<{}, RootState> = {
    actions: {
        /**
         * 下单报名参加赛事
         *
         * @param {} teamId
         * @param {} packageId
         * @param {} skuId
         * @param {} referrer 推荐人用户id
         */
        async createOrder({ state, commit }, { activityId, teamId, packageId, skuId, referrer }: { activityId: string, teamId: string, packageId: string, skuId: number, referrer: string | null }) {
            const orderResponse = await Vue.axios.post('/api/v1/participation/create_order', {
                activity_id: activityId,
                team_id: teamId,
                package_id: packageId,
                sku_id: skuId,
                referrer,
            });
            const orderRdata = orderResponse.data;
            if (orderRdata.status !== 0) {
                return {
                    status: 'error',
                    message: orderRdata.message,
                };
            }
            const orderId = orderRdata.data;
            const payResponse = await Vue.axios.post('/api/v1/participation/pay', {
                order_id: orderId,
                type: 'order',
            });
            const payRdata = payResponse.data;
            const ret = await chooseWXPay(payRdata.data.params);
            ret.type = 'jsapi';
            ret.orderId = orderId;
            return ret;
        },
        /**
         * 支付状态检查
         *
         * @param {} orderId 订单号
         * @param {} type  order | upgrade_order
         * @return {} rdata
         */
        async payStatus(context, { orderId, type = 'order' }: { orderId: string, type: string }) {
            const response = await Vue.axios.get('/api/v1/participation/pay_status', {
                params: {
                    id: orderId,
                    type,
                },
            });
            const rdata = response.data;
            return rdata;
        },
    },
};
export default m;
