import { CombinedVueInstance } from 'vue/types/vue';
import { Component, Mixins } from 'vue-property-decorator';
import PopupComponent from '@/components/messageBox/popup';
import { ROUTE_NAME } from '@/lib/constant';
import router from '@/router';

@Component({
    name: 'StartPopup',
    template: `<MyPopup v-model="value" class="start-popup" position="center" :modal="true">
        <img class="image" v-thumbnail="'/meta/activity_2/start_popup.png'" />
        <i class="icon" @click="onClickClose">&#xe60b;</i>
    </MyPopup>`,
})
export class StartPopup extends Mixins(PopupComponent) {
    /**
     * 点击关闭
     */
    public onClickClose() {
        this.value = false;
    }
    /**
     * 点击打卡
     */
    public onClickSignIn() {
        this.value = false;
        router.push({
            name: ROUTE_NAME.SIGN_IN_ENTER,
        });
    }
}

export type StartPopupComponent = CombinedVueInstance<StartPopup, object, object, object, Record<keyof object, any>>;
