function isInFirstDay() {
    const state = store.state.activity;
    const activityId = store.getters['activity/activityId'];
    if (isUndef(state.basicInfo[activityId])) {
        return false;
    }
    const now = serverDateNow().valueOf();
    const signInStartDate = dateParse(state.basicInfo[activityId]!.sign_in_start_date);
    return now.valueOf() >= signInStartDate.valueOf() && now.valueOf() < dateOffset(signInStartDate, 1).valueOf();
}
/**
 * 登录hook
 */
export async function login(to: Route, from: Route, next: (to?: any) => any) {
    // 不需要auth情况下，跳过登陆过程
    const auth = to.meta.auth;
    if (auth === false) {
        next();
        return;
    }
    // 获得query字符串
    const qs = getQS();

    // 尝试处理登录
    let jwt: string | null = qs.jwt as string;
    if (!jwt) {
        jwt = store.state.auth.jwt;
    }
    // 从storage获取jwt
    if (!jwt) {
        jwt = get('jwt');
    }

    let parsedJwt = null;
    if (jwt) {
        parsedJwt = decodeJwtPayload(jwt);
    }
    // 解析成功 并且 没有过期
    if (parsedJwt && Date.now() < parsedJwt.exp * 1000) {
        store.commit('auth/setJwt', jwt);
    }

    const isWeixin = is.weixin();
    if (isWeixin) {
        // 尝试处理微信登录
        if (typeof qs.code === 'string' && qs.code.length > 0) {
            store.commit('startRequest');
            // 存在跳转
            if (qs.redirect && qs.redirect.length > 0) {
                const redirectUrlComponent = url.parse(qs.redirect as string, true);
                redirectUrlComponent.search = undefined;
                // 写入code和state
                if (redirectUrlComponent.query) {
                    redirectUrlComponent.query.code = qs.code;
                    redirectUrlComponent.query.state = qs.state;
                } else {
                    redirectUrlComponent.query = {
                        code: qs.code,
                        state: qs.state,
                    };
                }

                const redirectUrl = url.format(redirectUrlComponent);
                location.replace(redirectUrl);
                return;
            }

            const loginRdata = await store.dispatch('auth/login', qs);
            if (loginRdata.status === 1) {
                location.replace(loginRdata.redirectUrl);
                return;
            }
            if (loginRdata.status === 3) {
                // 当前有一个请求，所以这次请求结果要求停止导航
                return;
            }
            delete qs.code;
            delete qs.state;
            const hash = decodeURIComponent(qs.hash as string);
            delete qs.hash;
            location.replace(`${location.protocol}//${location.host}${location.pathname}?${querystring.stringify(qs)}${hash}`);
            return;
        }

        // 在qs中添加hash
        qs.hash = encodeURIComponent(location.hash);
        // 尝试获取用户数据
        store.commit('startRequest');
        const loginRdata = await store.dispatch('auth/login', qs);
        store.commit('endRequest');
        if (loginRdata.status === 0) {
            // hash参数可以放弃
            delete qs.hash;
            // 矫正url
            const posInHref = location.href.indexOf('?');
            if (posInHref === -1 || location.href.indexOf('#') < posInHref) {
                location.replace(`${location.protocol}//${location.host}${location.pathname}?${querystring.stringify(qs)}${location.hash}`);
            } else {
                // 尝试获得basicInfo
                const state = store.state.user;
                if (!state.basicInfo) {
                    const activityIds = store.getters['activity/activityIds'] as string[];
                    store.dispatch('getProvinceList');
                    await Promise.all([
                        store.dispatch('user/getBasicInfo'),
                        ...activityIds.map((activityId) => store.dispatch('activity/getBasicInfo', {
                            activityId,
                        })),
                    ]);
                }
                next();
            }
        } else if (loginRdata.status === 1) { // 准备执行重定向(例如OAuth)
            location.replace(loginRdata.redirectUrl);
        }
    } else {
        // 缺少jwt情况下执行重新登录

        const qs = getQS();
        if (qs.env === 'gio' || is.tourye() || is.bairipao()) {
            if (!store.state.auth.jwt) {
                next({
                    name: ROUTE_NAME.USER_PHONE_LOGIN,
                });
            } else {
                // 尝试获得basicInfo
                const state = store.state.user;
                if (!state.basicInfo) {
                    const activityIds = store.getters['activity/activityIds'] as string[];
                    store.dispatch('getProvinceList');
                    await Promise.all([
                        store.dispatch('user/getBasicInfo'),
                        ...activityIds.map((activityId) => store.dispatch('activity/getBasicInfo', {
                            activityId,
                        })),
                    ]);
                }
                next();
            }
        }
    }
}

/**
 * 检查是否引导手机号绑定
 */
export async function mobileBind(to: Route, from: Route, next: (to?: any) => any) {
    const basicInfo = store.state.user.basicInfo;
    if (basicInfo && !basicInfo.mobile_bound && to.meta && to.meta.needBinding) {
        next({
            name: ROUTE_NAME.USER_PHONE_BINDING,
            params: {
                routeTo: to.path,
            },
        });
    } else {
        next();
    }
}

/**
 * 完成jssdk的config
 */
export async function jssdkConfig(to: Route, from: Route, next: (to?: any) => any) {
    // 对于不需要auth情况下，不使用jssdk
    const meta = to.meta;
    if ((meta && meta.auth === false) || !is.weixin()) {
        return next();
    }
    if (store.state.jssdk.jssdkConfig) {
        return next();
    }
    // 获取JS-SDK
    store.dispatch('getJSSDKParams', {
        url: encodeURIComponent(location.href.substr(0, location.href.indexOf(location.hash))),
    }).then((params) => {
        jssdk.config({
            appId: params.appId, // 必填，公众号的唯一标识
            timestamp: params.timestamp, // 必填，生成签名的时间戳
            nonceStr: params.nonceStr, // 必填，生成签名的随机串
            signature: params.signature, // 必填，签名，见附录1
            jsApiList: [
                'onMenuShareAppMessage',
                'onMenuShareTimeline',
                'chooseImage',
                'previewImage',
                'uploadImage',
                'downloadImage',
                'closeWindow',
                'hideOptionMenu',
                'chooseWXPay',
                'getLocation',
                'getLocalImgData',
                'hideMenuItems',
                'showMenuItems',
            ], // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
        jssdk.error((res) => {
            MessageBox({
                title: '错误',
                message: `部分配置信息失效(${res.errMsg})，可能导致部分功能不可用`,
                confirmButtonText: '刷新',
                cancelButtonText: '继续',
                showCancelButton: true,
            })!.then((action: any) => {
                if (action === 'confirm') {
                    location.reload();
                }
            });
        });
    });
    next();
}

/**
 * 设置title
 */
export async function setCommonTitle(to: Route, from: Route, next: (to?: any) => any) {
    // 没有登录信息
    if (!store.state.auth.jwt || (to.meta && to.meta.shareWord === false)) {
        next();
        return;
    }
    // tip: 不针对meta.disableShare做处理，避免前一个页面registerShare数据带到这个页面

    const basicInfo = store.state.user.basicInfo;
    const activityId = to.params.activityId || store.getters['activity/activityId'];
    const qs = getQS();
    if (basicInfo) {
        qs.referrer = String(basicInfo!.id);
    }
    const info = store.state.activity.basicInfo[activityId];
    if (!isUndef(info)) {
        const data = {
            join_index: '' + info!.join_index,
            user_name: basicInfo!.nickname,
            team_name: info!.team_name,
        };
        registerShare({
            title: info!.joined ? replace(info!.share_title_joined, data) as string : replace(info!.share_title_not_joined, data) as string,
            desc: info!.joined ? replace(info!.share_text_joined, data) as string : replace(info!.share_text_not_joined, data) as string,
            link: `${window.pageUrl}${window.appPageRoot}?${querystring.stringify(qs)}#${to.path}`,
            imgUrl: info!.share_logo,
        });
    } else {
        registerShare({
            title: '百日跑默认分享标题',
            desc: '百日跑默认分享文本',
            link: `${window.pageUrl}${window.appPageRoot}?${querystring.stringify(qs)}#${to.path}`,
            imgUrl: '',
        });
    }
    next();
}

/**
 * 上报邀请者
 */
export async function recordReferrer(to: Route) {
    // 没有登录时，不发送请求
    if (!store.state.auth.jwt) {
        return;
    }
    const referrer = getQS().referrer as string;
    if (recordReferrerStat.done || recordReferrerStat.req || !referrer) {
        return;
    }
    if (isNaN(Number(referrer))) {
        // 主动捕获错误或异常
        Sentry.captureException(new Error('invalid referrer'));
        return;
    }
    recordReferrerStat.req = true;
    const rdata = await store.dispatch('user/recordReferrer', referrer);
    if (rdata.status === 0) {
        recordReferrerStat.done = true;
    }
    recordReferrerStat.req = false;
}

/**
 * 禁止分享
 */
export function disableShareHook(to: Route, from: Route, next: (to?: any) => any) {
    if (to.meta && to.meta.disableShare) {
        disableShare();
    }
    next();
}

/**
 * 允许分享
 */
export function enableShareHook(route: Route) {
    if (route.meta && route.meta.disableShare) {
        enableShare();
    }
}

export function setTitle(to: Route) {
    if (to.meta && to.meta.title) {
        const title = isFunction(to.meta.title) ? to.meta.title(to) : to.meta.title;
        setWechatTitle(title);
    } else {
        setWechatTitle('百日跑');
    }
}

/**
 * 设置页面变量
 */
export function setReportData(to: Route) {
    if (typeof gio !== 'undefined') {
        const srctag = getQS().srctag as string;
        if (srctag) {
            gio('page.set', { srctag });
        }
    }
}

export function blockPlatform(to: Route, from: Route, next: (to?: any) => any) {
    if (!to.meta || getQS().env === 'gio') {
        next();
        return;
    }
    const platforms = to.meta.blockPlatform;
    if (!platforms) {
        next();
        return;
    }
    if (!isArray(platforms)) {
        next();
        return;
    }
    if (platforms.indexOf(getPlatform()) >= 0) {
        next(false);
        return;
    }
    next();
}

/**
 * 检查开跑第一天
 */
export function checkFirstDayPopup(to: Route) {
    queue.promise((resolve, reject) => {
        const activityId = store.getters['activity/activityId'];
        const hasPopup = get(`hundred_${activityId}_first_day_popup`);
        if (!isInFirstDay() || hasPopup) {
            resolve();
            return;
        }
        let popup: StartPopupComponent;
        // @ts-ignore
        popup = new StartPopup({
            el: document.createElement('div'),
        });
        document.body.appendChild(popup.$el);
        popup.value = true;
        set(`hundred_${activityId}_first_day_popup`, '1');
        popup.onClose = () => {
            resolve();
        };
    });
}

/**
 * 检查其他的popup
 */
export function checkMarketPopup(to: Route) {
    queue.promise((resolve, reject) => {
        const route = router.currentRoute;
        if (!isPopupDisplay(route, 'market')) {
            resolve();
            return;
        }
        const hasPopup = get('market_20190720_popup');
        const now = dateNow();
        if (!hasPopup && now.valueOf() >= dateParse('2019-07-20 00:00:00').valueOf() && now.valueOf() <= dateParse('2019-07-20 24:00:00').valueOf()) {
            let popup: MarketPopupComponent;
            // @ts-ignore
            popup = new MarketPopup({
                el: document.createElement('div'),
            });

            document.body.appendChild(popup.$el);
            popup.value = true;
            set('market_20190720_popup', '1');
            popup.onClose = () => {
                resolve();
            };
        } else {
            resolve();
        }
    });
}

/** 检查是否需要显示引导加入战队弹窗 */
export async function checkJoinTeamHint(to: Route, from: Route) {
    queue.promise((resolve, reject) => {
        const route = router.currentRoute;
        const name = route.name;
        if (name !== ROUTE_NAME.ACTIVITY && name !== ROUTE_NAME.SIGN_IN_ENTER && name !== ROUTE_NAME.COMMUNITY && name !== ROUTE_NAME.USER_HOME ) {
            resolve();
            return;
        }
        // todo: 这里从默认写死改成使用getter
        const activityId = store.getters['activity/activityId'];
        const basicInfo = store.state.activity.basicInfo[activityId];
        if (basicInfo && basicInfo.joined && joinTeamHintVisible && basicInfo.startParticipation && !basicInfo.closeParticipation) {
            if (!basicInfo.team_id) {
                // 没有选择战队
                store.commit('setPopupContent', {
                    src: 'https://static.run100.runorout.cn/meta/team/join_hint_2.png',
                    desc2: '当前您还未选择战队<br>请您加入战队，和小伙伴们一起奔跑吧',
                    actionText: '加入战队',
                    actionCb: () => {
                        router.push({
                            name: ROUTE_NAME.ACTIVITY_SELECT_TEAM,
                            params: {
                                // todo: 目前只能默认是2
                                activityId,
                            },
                            query: {
                                step: '1',
                            },
                        });
                        store.commit('setPopupVisible', false);
                        resolve();
                    },
                });
                store.commit('setPopupVisible', true);
            }
            joinTeamHintVisible = false;
        } else {
            resolve();
        }
    });
}

/** 倒计时弹窗 */
export function checkCountdownPopup(to: Route, from: Route) {
    queue.promise((resolve, reject) => {
        const route = router.currentRoute;
        if (!isPopupDisplay(route, 'countdown')) {
            resolve();
            return;
        }

        const now = dateNow();
        const date = dateFormat(now, 'Y-m-d');
        const key = `market_${date}_popup`;

        if (!get(key) && datePopup[date]) {
            let popup: CountdownPopupComponent;
            // @ts-ignore
            popup = new CountdownPopup({
                el: document.createElement('div'),
            });

            document.body.appendChild(popup.$el);
            popup.value = true;
            set(key, '1');
            popup.onClose = () => {
                resolve();
            };
        } else {
            resolve();
        }
    });
}

