/** 检测是否需要显示基因测试弹窗 */
export async function checkShowGenePopup(to: Route, from: Route) {
    /** 持续到 9.10 */
    const now = serverDateNow().valueOf();
    const isEnd = now > new Date('2019/12/01').valueOf();
    if (isEnd) {
        return;
    }
    queue.promise(async (resolve, reject) => {
        const route = router.currentRoute;
        if (( route.params.activityId === '2' && route.name === ROUTE_NAME.ACTIVITY) || route.name === ROUTE_NAME.COMMUNITY || route.name === ROUTE_NAME.USER_HOME) {
            const activityId = '2';
            const basicInfo = store.state.activity.basicInfo[activityId];
            await store.dispatch('activity/getPackage', {
                activityId,
            });
            const packageIds = store.state.activity.activityPackages[activityId] || [];
            if (get(KEY_GENE_POPUP) === 'hide') {
                resolve();
                return;
            }
            // 报名日期内 已经报名的 没有买基因的同学显示弹窗
            if (basicInfo!.startParticipation && basicInfo!.joined && basicInfo!.package_id === packageIds[0]) {
                const type = Math.floor(Math.random() * 2);

                let popup2: GenePopupComponent2;
                // @ts-ignore
                popup2 = new GenePopup2({
                    el: document.createElement('div'),
                    props: {
                        type,
                    },
                });
                document.body.appendChild(popup2.$el);

                switch (type) {
                case 0:
                    popup2.type = 0;
                    break;
                case 1:
                case 2:
                    popup2.type = 1;
                    break;
                }
                popup2.value = true;
                gio('page.set', { ab_test: type === 0 ? 'gene_popup_type_4' : 'gene_popup_type_5' });
                popup2.onClose = () => {
                    set(KEY_GENE_POPUP, 'hide');
                    resolve();
                };
            } else {
                resolve();
            }
        } else {
            resolve();
        }
    });
}

// 检查是否是队长(不是队长禁止进入队长的某些页面)
export async function checkMonitor(to: Route, from: Route, next: any) {
    if (to.meta && to.meta.checkMonitor) {
        await store.dispatch('monitor/getTeamList', {
            activityId: to.params.activityId,
        });
        const isMonitorTeam = (store.state.monitor.teamListMap[to.params.activityId] || []).length > 0;
        if (!isMonitorTeam) {
            next({
                name: ROUTE_NAME.ACTIVITY,
                params: {
                    activityId: to.params.activityId,
                },
                replace: true,
            });
        } else {
            next();
        }
    } else {
        next();
    }
}

