import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import { dateParse, dateToday, dateFormat, getYear, getMonth } from '@/lib/utils';
import { stat } from 'fs';

export interface SignInState {
    /** 打卡数据 */
    signInData: {
        today_sign_in: { // 未报名/未打卡/报名了不在打卡期 today_sign_in为null
            image: FileId,
            image_url: string,
            time: number,
            distance: string,
            status: 'normal' | 'accepted' | 'rejected',
        },
        total_days: number, // 未报名当前赛事，没有这个值/这个值表示在当前赛事中的打卡总天数
        total_distance: number,  // 未报名当前赛事，没有这个值/这个值表示在当前赛事中的打卡总距离
    } | null;
    /** 打卡日历数据 */
    signInMap: {
        [date: string]: SignIn.AggrDetail | undefined,
    };
    /** 是否加载过日历数据 */
    calendarCached: {
        [month: string]: boolean | undefined,
    };
    /** 打卡数据 */
    detail: {
        [id: string]: SignIn.Detail | undefined,
    };
    rejectedDetail: {
        [id: string]: SignIn.RejectedDetail | undefined,
    };
    /** 勋章id */
    badgesIds: number[] | null;
    signInAd: Activity.Ad[];
}

const m: Module<SignInState, RootState> = {
    namespaced: true,
    state: {
        signInData: null,
        signInMap: {},
        calendarCached: {},
        detail: {},
        rejectedDetail: {},
        badgesIds: null,
        signInAd: [],
    },
    mutations: {
        setSignInData(state, data) {
            state.signInData = data;
        },
        /** 填充打卡日历数据 */
        setSignIn(state, { date, signIn }: { date: string, signIn: SignIn.AggrDetail }) {
            Vue.set(state.signInMap, date, signIn);
        },
        /**
         * 记录日历是否已经加载
         */
        setCalendarCached(state, { month, cached }) {
            Vue.set(state.calendarCached, month, cached);
        },
        /**
         * 记录detail
         */
        setDetail(state, { signInId, detail }) {
            Vue.set(state.detail, signInId, detail);
        },
        /**
         * 记录不通过打卡详情
         */
        setRejectedDetail(state, { signInId, detail }) {
            Vue.set(state.rejectedDetail, signInId, detail);
        },
        /**
         * 设置点亮的勋章的ids
         */
        setBadgesIds(state, ids: number[]) {
            state.badgesIds = ids;
        },
        /** 存储打卡页广告 */
        setSignInAd(state, ads: Activity.Ad[]) {
            state.signInAd = ads;
        },
    },
    actions: {
        /**
         * 创建打卡
         * @param participationId 允许为空
         */
        async create({ state, commit, dispatch }, { image, time, distance, crossCountryRun }) {
            const response = await Vue.axios.post('/api/v1/participation/create_sign_in', {
                image,
                time,
                distance,
                cross_country_run: crossCountryRun,
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setBadgesIds', rdata.data.medal_ids);
                const today = dateToday();
                const month = `${getYear(today)}-${getMonth(today) + 1}`;
                await Promise.all([
                    state.signInData ? dispatch('getSignInData', { forceUpdate: true }) : '',
                    state.calendarCached[month] ? dispatch('getCalendar', { month, forceUpdate: true }) : '',
                ]);

            }
            return rdata;
        },
        /**
         * 获取打卡数据
         */
        async getSignInData({ state, commit }, { forceUpdate } = {}) {
            if (state.signInData && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/participation/sign_in_data');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setSignInData', rdata.data);
            }
            return rdata;
        },
        /**
         * 按月份获取打卡数据
         * @param month 格式2018-01
         */
        async getCalendar({ state, commit }, { month, forceUpdate } = {}) {
            if (state.calendarCached[month] && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/participation/sign_in_calendar', {
                params: {
                    month,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                for (const signIn of rdata.data as SignIn.AggrDetail[]) {
                    commit('setSignIn', {
                        date: signIn.date,
                        signIn,
                    });
                    commit('setCalendarCached', {
                        month,
                        cached: true,
                    });
                }
            }
            return rdata;
        },
        /**
         * 获取打卡详情
         */
        async getDetail({ state, commit }, { signInId, forceUpdate }) {
            const response = await Vue.axios.get('/api/v1/participation/sign_in_detail', {
                params: {
                    id: signInId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setDetail', {
                    signInId,
                    detail: rdata.data,
                });
            }
            return rdata;
        },
        /**
         * 获取不通过的打卡详情
         */
        async getRejectedDetail({ state, commit }, { signInId, forceUpdate }) {
            const response = await Vue.axios.get('/api/v1/participation/rejected_sign_in_detail', {
                params: {
                    sign_in_id: signInId,
                },
            });
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setRejectedDetail', {
                    signInId,
                    detail: rdata.data,
                });
            }
            return rdata;
        },
        /**
         * 获取打卡页底部广告
         */
        async getSignInAd({ state, commit }, forceUpdate) {
            if (state.signInAd && !forceUpdate) {
                return;
            }
            const response = await Vue.axios.get('/api/v1/framework/sign_in_ads');
            const rdata = response.data;
            if (rdata.status === 0) {
                const data = rdata.data;
                commit('setSignInAd', data);
            }
            return rdata;
        },
        /**
         * 打卡申诉
         */
        async postSignInAppeal({ state, commit }, { signInId, reason }: { signInId: string, reason: string }) {
            const response = await Vue.axios.post('/api/v1/participation/sign_in_appeal', {
                sign_in_id: signInId,
                reason,
            });
            const rdata = response.data;
            return rdata;
        },
    },
};

export default m;
