import Schema, { Model } from '@forzoom/models';
import { RuntimeType } from '.';
@RuntimeType({
    models: {
        activityId: { type: 'String', default: '' },
        logo: { type: 'String', default: '' },
        logoUrl: { type: 'String?', default: null },
        name: { type: 'String', default: '' }, // 战队名称
        cityId: { type: 'Number?' },
        cityName: { type: 'String?' },
        levelId: { type: 'String', default: '' },
        label: { type: 'String', default: '' },
        /** 描述 */
        description: { type: 'String', default: '' },
        groupQrCode: { type: 'String?' },
        groupQrCodeUrl: { type: 'String?', default: null },
        monitorQrCode: { type: 'String?' },
        monitorQrCodeUrl: { type: 'String?', default: null },
        /** 生活照片 */
        photos: { type: 'Array', default() { return []; } },
        /** 生活照片url */
        photosUrl: { type: 'Array', default() { return []; } },
    },
})
export default class TeamApply extends Schema {
    public static models?: Model;
    /** 活动id */
    public activityId!: number | null;
    /** 封面id */
    public logo!: FileId | null;
    /** 封面url */
    public logoUrl!: string | null;
    /** 名称 */
    public name!: string;
    /** 城市id */
    public cityId!: number | null;
    /** 城市 */
    public cityName!: string | null;
    /** 组别id */
    public levelId!: string;
    /** 描述 */
    public description!: string;
    /** 群二维码key */
    public groupQrCode!: FileId | null;
    /** 群二维码url */
    public groupQrCodeUrl!: string | null;
    /** 队长二维码key */
    public monitorQrCode!: FileId | null;
    /** 队长二维码url */
    public monitorQrCodeUrl!: string | null;
    /** 生活照片key */
    public photos!: string[];
    /** 生活照片url */
    public photosUrl!: string[];
    /** 战队标签 */
    public label!: string;

    constructor(data?: object) {
        super(TeamApply.models!, data);
        this._name = TeamApply.name;
    }
}
