<template>
    <div class="swiper-indicator">
        <div v-for="(item, index) in list"
            :key="index"
            class="indicator"
            :class="{active: value == item}"
            @click="onClick(item)">
        </div>
    </div>
</template>

<script>
export default {
    name: 'SwiperIndicator',
    props: {
        value: {
            type: [
                String,
                Number,
            ],
        },
        // 希望输入一个数组
        indicators: {
            type: Array,
        },
        // 长度
        length: {
            type: Number,
        },
    },
    computed: {
        list() {
            if (this.indicators) {
                return this.indicators;
            } else {
                const arr = [];
                for (let i = 0; i < this.length; i++) {
                    arr.push(i);
                }
                return arr;
            }
        },
    },
    methods: {
        onClick(value) {
            this.$emit('input', value);
        },
    },
};
</script>

<style lang="less">
    @import "../../lib/style/util.less";
    @import "../../lib/style/mixins.less";

    .swiper-indicator {
        text-align: center;

        .indicator {
            display: inline-block;
            .px2rem6(margin, 0, 5);
            .px2rem6(width, 8);
            .px2rem6(height, 8);
            background-color: #ccc;
            border-radius: 50%;
            transition: background-color 0.2s;
        }
        .indicator.active {
            background-color: @color-white;
            box-shadow: 0 2px 2px rgba(0,0,0,0.2);
        }
    }
</style>