
import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
    name: 'Toast',
})
export default class Toast extends Vue {
    @Prop({
        type: String,
    }) public message!: string;
    @Prop({
        type: String,
        default: '',
    }) public className!: string;
    @Prop({
        type: String,
        default: 'middle',
    }) public position!: string;
    @Prop({
        type: String,
        default: '',
    }) public iconClass!: string;

    public visible: boolean = false;

    public get customClass() {
        const classes = [];
        switch (this.position) {
        case 'top':
            classes.push('is-placetop');
            break;
        case 'bottom':
            classes.push('is-placebottom');
            break;
        default:
            classes.push('is-placemiddle');
        }
        classes.push(this.className);

        return classes.join(' ');
    }

    public render(h: any) {
        const content = h('div', {
            class: [
                'mint-toast',
                this.customClass,
            ],
            style: {
                display: this.visible ? 'block' : 'none',
                padding: this.iconClass === '' ? '10px' : '20px',
            },
        }, [
            this.iconClass !== '' ? h('i', {
                class: [
                    'mint-toast-icon',
                    this.iconClass,
                ],
            }) : null,
            h('span', {
                class: 'mint-toast-text',
                style: {
                    'padding-top': this.iconClass === '' ? '0' : '10px',
                },
            }, this.message),
        ]);
        return h('transition', {
            props: {
                name: 'mint-toast-pop',
            },
        }, [
            this.visible ? content : null,
        ]);
    }
}
