import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import Identification from '@/models/identification';
export interface IdentificationState {
    /**
     * 用户已经进行过跑团认证则返回相应信息，否则返回null
     */
    apply: Identification | null;
}
const m: Module<IdentificationState, RootState> = {
    namespaced: true,
    state: {
        apply: null,
    },
    mutations: {
        /**
         * 记录申请
         * @param {} apply 申请数据
         */
        setApply(state, apply) {
           state.apply = apply;
        },
    },
    actions: {
        /**
         * 获得战队申请信息
         * @param {} forceUpdate
         */
        async getIdentificationApply({ state, commit }, forceUpdate) {
            // if (state.apply && !forceUpdate) {
            //     return;
            // }
            const response = await Vue.axios.get('/api/v1/running_group');
            const rdata = response.data;
            if (rdata.status === 0) {
                commit('setApply', new Identification(rdata.data || {}));
            }
            return rdata;
        },
        /**
         * 申请创建战队
         * @param {} data 数据
         */
        async setIdentificationApply({ state, commit, dispatch }) {
            // if (state.apply) {
            //     await dispatch('getIdentificationApply');
            // }
            const response = await Vue.axios.post('/api/v1/running_group', state.apply!.getSnake());
            const rdata = response.data;
            return rdata;
        },
    },
};
export default m;
