/** 可换套餐期限 */
function upgradeDeadline(basicInfo: Activity.BasicInfo) {
    const joined = basicInfo ? basicInfo.joined : false;
    if (joined && basicInfo!.can_change_package) {
        const now = dateNow().getTime();
        const threeDays = dateOffset(dateParse(basicInfo!.join_time), 3);
        const finish = threeDays.getTime();
        const leftSecond = Math.floor((finish - now) / 1000);
        if (leftSecond > 0) {
            const arr = dateFormat(threeDays).split('-');
            return `限${Number(arr[1])}月${Number(arr[2])}日前`;
        } else {
            return null;
        }
    } else {
        return null;
    }
}

